<template>
  <div class="ownScrollbar" style="margin-left:50px;min-height: 400px;max-height: 600px;overflow-y: scroll;">
    <el-row><!--<el-col>A B C D E ...</el-col>--></el-row>
    <el-form :model="dataForm" :rules="dataRule" ref="dataForm" label-width="80px" size="small" :inline="true">
      <el-form-item label="" prop="UserName" style="margin-top: 15px">
        <el-input v-model="dataForm.UserName" placeholder="" style="width: 160px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="Sex" style="margin-top: 15px">
        <el-radio-group v-model="dataForm.Sex" style="width: 144px">
          <el-radio label="1"></el-radio>
          <el-radio label="2"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="BirthDateAge" style="margin-top: 15px">
        <el-input v-model="dataForm.BirthDateAge" @blur="stampCalc(dataForm.BirthDateAge, dataForm.BirthDateUnit)" placeholder="" style="width: 91px" clearable></el-input>
        <el-select v-model="dataForm.BirthDateUnit" @change="stampCalc(dataForm.BirthDateAge, dataForm.BirthDateUnit)" style="width: 66px;margin-right: 10px">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="0"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="BirthDate" style="margin-top: 15px">
        <el-date-picker type="date" value-format="yyyy-MM-dd" v-model="dataForm.BirthDate"
                        placeholder="" style="width: 160px">
        </el-date-picker>
      </el-form-item>

      <el-form-item label="" prop="MobilePhone" style="margin-top: 15px">
        <el-input v-model="dataForm.MobilePhone" placeholder="" style="width: 160px"></el-input>
      </el-form-item>
      <br>
      <el-form-item label="" prop="Source">
        <el-select v-model="dataForm.Source" style="width: 160px">
          <el-option v-for="item in memberOrigin" :key="item.lab"
                     :label="item.lab" :value="item.val">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="" prop="AllergyHistory" style="margin-top: 15px">
        <el-input v-model="dataForm.AllergyHistory" placeholder="" style="width: 413px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="Address" style="margin-top: 15px">
        <el-input v-model="dataForm.Address" placeholder="" style="width: 413px"></el-input>
      </el-form-item>
      <!--<el-form-item label="" prop="SalePrice">-->
      <!--<el-input v-model="dataForm.SalePrice" placeholder=""></el-input>-->
      <!--</el-form-item>-->
    </el-form>

    <div style="text-align: right; margin-top: 210px">
      <el-button @click="$store.commit('setRegisterStep', 2); $emit('childEven')"></el-button>
      <el-button type="primary" @click=" $store.commit('setRegisterStep', 2); dataFormSubmit()"></el-button>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import API from '@/api'
import {Currency, Phone, NumberInt} from '../../utils/validate'
import {formatDate, calcAge, calcTimeStamp} from '@/utils/validate'
export default {
  data () {
    return {
      // visible: false,
      dataListLoading: false, // 
      dataForm: {
        UserName: '',
        Sex: '1',
        BirthDate: '',
        BirthDateAge: '',
        BirthDateUnit: '1', // str
        MobilePhone: '',
        AllergyHistory: '', // 
        Source: '20', // 
        Address: ''
      },
      memberOrigin: [
        {lab: '', val: '1'},
        {lab: '', val: '2'},
        {lab: '', val: '3'},
        {lab: '', val: '4'},
        {lab: '', val: '5'},
        {lab: '', val: '6'},
        {lab: '', val: '20'}
      ],
      dataRule: {
        UserName: Currency(''),
        BirthDateAge: NumberInt(),
        BirthDate: Currency(''),
        Source: Currency(''),
        MobilePhone: Phone(1)
      },
      dataList: null
    }
  },
  watch: {
    'dataForm.BirthDate': function (val, oldval) {
      if (val === null) {
        this.dataForm.BirthDateUnit = '1'
        this.dataForm.BirthDateAge = ''
      } else {
        var allAge = calcAge(val) // !!!!!!18  10 1
        if (allAge.substr(allAge.length - 1) === '') {
          this.dataForm.BirthDateUnit = '0'
        } else if (allAge.substr(allAge.length - 1) === '') {
          this.dataForm.BirthDateUnit = '1'
        }
        this.dataForm.BirthDateAge = allAge.substring(0, allAge.length - 1) // !!!!!!parseInt
      }
    }
  },
  methods: {
    // age
    stampCalc (age, unit) {
      if (unit === '1' && age > 120) { // 120
        this.dataForm.BirthDateUnit = '1'
        this.dataForm.BirthDateAge = ''
        return false
      } else if (unit === '0' && age > 12) { // 12
        this.dataForm.BirthDateUnit = '1'
        this.dataForm.BirthDateAge = ''
        return false
      }

      if (age === '' || age === '0' || !/^\d+$/.test(age)) {
        this.dataForm.BirthDateUnit = '1'
        this.dataForm.BirthDateAge = ''
        return false
      }
      this.dataForm.BirthDate = calcTimeStamp(age, unit)
    },
    child () {
      this.$emit('childEven')
    },
    // info
    init () {},
    handleClose () {
      this.dataForm = {
        UserName: '',
        Sex: '1',
        BirthDate: '',
        BirthDateAge: '',
        BirthDateUnit: '1', // str
        MobilePhone: '',
        AllergyHistory: '', // 
        Address: '',
        Source: '20' // 
      }
    },
    // 
    dataFormSubmit () {
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          var paramsAdd = {
            StoreId: this.$store.getters.getAccountCurrentHandleStore,
            UserName: this.dataForm.UserName,
            Sex: this.dataForm.Sex,
            BirthDate: this.dataForm.BirthDate,
            MobilePhone: this.dataForm.MobilePhone,
            AllergyHistory: this.dataForm.AllergyHistory,
            Address: this.dataForm.Address,
            Source: this.dataForm.Source
          }
          console.log(paramsAdd)
          API.member.addMemberSubmit(paramsAdd).then((data) => {
            if (data.code === '0000') {
              this.$message({
                message: `${''}`,
                type: 'success',
                duration: 1500,
                onClose: () => {
                  // this.visible = false
                  this.$emit('childEven')
                }
              })
            } else {
              this.$message.error(data.message)
            }
          })
        }
      })
    }
    // ,
    // addOrUpdateHandle3 (id, type) {
    //   this.addOrUpdateVisible = true
    //   this.$nextTick(() => {
    //     this.$refs.addOrUpdate.init(id, type)
    //   })
    // }
  }
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
.ownScrollbar::-webkit-scrollbar,
.purchaseListInfo /deep/ .el-table--scrollable-y .el-table__body-wrapper::-webkit-scrollbar {
  width: 7px;
}
.ownScrollbar::-webkit-scrollbar-thumb,
.purchaseListInfo /deep/ .el-table--scrollable-y .el-table__body-wrapper::-webkit-scrollbar-thumb {
  border-radius: 3px;
  box-shadow: inset 0 0 5px rgba(0,0,0,0.1);
  background-color: #DDDEE0;
}
.ownScrollbar::-webkit-scrollbar-track,
.purchaseListInfo /deep/ .el-table--scrollable-y .el-table__body-wrapper::-webkit-scrollbar-track {
  border-radius: 0;
  box-shadow: inset 0 0 5px rgba(0,0,0,0);
  background-color: rgba(0,0,0,0);
}
</style>



