    <el-form ref="dataForm" :rules="dataRule" :model="dataForm" label-width="80px" size="mini">
      <el-container class="doctor-recipel" style="padding-left: 20px">
        <el-aside width="67%" style="border-right: 1px solid #DCDFE6; padding-right: 5px;min-width: 700px;">
          <div id="leftHeightPatient">
            <p style="text-align: center;font-size: 16px; padding: 5px 0 20px 0;">
              <span style="position: relative; top: 10px;font-size: 18px; font-weight: 600;cursor: pointer"
                    @click="cutOut = !cutOut" :title="cutOut ? '' : ''"> 
                <b style="font-weight: 700;font-size: 12px;color: #409EFF;
                   text-decoration: underline">{{cutOut ? '' : ''}}</b>
              </span>
            </p>

            <transition>
              <div v-show="cutOut">
                <el-row>
                  <el-col :span="12">
                    <el-form-item label="">
                      <el-input v-model="dataForm.PrescriptionName" placeholder="" style="width: 75%" :disabled="openType === 'see' ? true : false"></el-input>
                    </el-form-item>
                  </el-col>
                  <!--<el-col :span="12">-->
                    <!--<el-form-item label="">-->
                      <!--<el-input v-model="dataForm.AgoIllness" placeholder="" style="width: 75%" :disabled="openType === 'see' ? true : false"></el-input>-->
                    <!--</el-form-item>-->
                  <!--</el-col>-->
                </el-row>
                <el-row>
                  <el-col :span="24">
                    <el-form-item label="">
                      <el-radio-group v-model="dataForm.CategoryOne"  @change="handleChange" size="mini">
                        <el-radio label="1" border :disabled="openType === 'see' ? true : false"></el-radio>
                        <el-radio label="3" border :disabled="openType === 'see' ? true : false"></el-radio>
                        <el-radio label="4" border :disabled="openType === 'see' ? true : false"></el-radio>
                        <el-radio label="5" border :disabled="openType === 'see' ? true : false"></el-radio>
                        <el-radio label="2" border :disabled="openType === 'see' ? true : false"></el-radio>
                      </el-radio-group>
                    </el-form-item>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="24">
                    <el-form-item label="">
                      <el-input v-model="dataForm.MainCure" placeholder="" style="width: 75%" :disabled="openType === 'see' ? true : false"></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="24">
                    <el-form-item label="">
                      <el-input v-model="dataForm.Effect" placeholder="" style="width: 75%" :disabled="openType === 'see' ? true : false"></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="12">
                    <el-form-item label="">
                      <el-input v-model="dataForm.Explain" placeholder="" style="width: 75%" :disabled="openType === 'see' ? true : false"></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="12">
                    <el-form-item label="" prop="DrugRate">
                      <el-input v-model="dataForm.DrugRate" placeholder="" style="width: 75%" :disabled="openType === 'see' ? true : false"></el-input>
                    </el-form-item>
                  </el-col>
                </el-row>
              </div>
            </transition>
            <div style="border-bottom: 1px solid #E6E6E6; font-weight: 500">R:</div>
          </div>
          <!---->
          <el-table
            :height="chenxiHeight"
            :data="leftTableData"
            stripe
            v-loading="dataListLoading"
            row-class-name="purchaseListRow"
            :header-cell-style="$cxObj.tableHeaderStyle30px"
            style="width: 100%;">
            <component :is="leftTable" ref="leftCurrentTable" @tableEvent="delDrugs" @tableNumberEvent="consoleTable"
                       :fatherDataForm="openType"></component>
          </el-table>
        </el-aside>

        <!--tabs-->
        <el-main width="30%" style="padding: 10px 10px 0; border-bottom: 1px solid #DCDFE6;">
          <el-input v-model="dataForm.SpellName" @blur="dataForm.SpellName = ''"
                    :placeholder="`, ${$store.getters.getAccountCurrentHandleStore}`"
                    style="min-width: 190px; width: 100%" size="small" clearable><i slot="prefix" class="el-input__icon el-icon-search"></i>
          </el-input>
          <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane v-for="item in drugsCategoryArr" :key="item.id" :label="item.text" :name="item.id" :disabled="openType === 'see' ? true : false"></el-tab-pane>
          </el-tabs>
          <div class="rightUlStyle">
            <ul class="ownScrollbar xx">
              <li v-for="item in rightUlData" :key="item.Id">
                <el-tooltip class="item" effect="light" :content="item.ShowName+' ['+item.Quantity+']'" placement="left">
                  <el-row style="clear: both">
                    <!--<span v-text="item.ShowName === null ? '000' : item.ShowName"></span> {{item.Id}}{{item.Quantity}}g/{{item.RedLine}}-->

                    <!--++-->
                    <el-col :span="6">
                    <span v-text="item.ShowName === null ? '' : item.ShowName"
                          style="display: inline-block; vertical-align: middle; width: 50px;overflow: hidden; white-space: nowrap;text-overflow: ellipsis">
                    </span>
                    </el-col>
                    <el-col :span="12" :style="{color: item.Quantity - item.RedLine > 0 ? '#333' : item.Quantity === 0 ? '#ccc' : '#e4392c'}">
                    <span style="display: inline-block; vertical-align: middle;text-align: left;padding-left: 10px;
                                 overflow: hidden;width: 100px; white-space: nowrap;text-overflow: ellipsis">
                      {{item.Quantity}} / {{item.RedLine}}
                      <!--{{item.Id}} {{item.Quantity}}/{{item.RedLine}}-->
                    </span>
                    </el-col>

                    <el-col :span="6" style="text-align: right;padding-right: 7px">
                      <el-button type="text" size="mini" @click="addDrugs(item)"
                                 style="font-size: 15px;font-weight: 600" v-if="openType === 'see' ? false : true"></el-button>
                      <!--<el-button type="text" size="mini" @click="cutOut = false; addDrugs(item)"></el-button>-->
                    </el-col>
                  </el-row>
                </el-tooltip>
              </li>
            </ul>

            <!--A B C D...-->
            <ol>
              <li v-for="(item, ind) in litterArr" :key="item.content" :class="[item.isActive ? 'isActive' : '']"
                  v-text="item.content" @click="activeLitter(item.content, ind)">
              </li>
            </ol>
          </div>

          <div class="rightPageCounterContainer">
            <div class="rightPageCounter">
              <el-pagination @size-change="sizeChangeHandle"
                             @current-change="currentChangeHandle"
                             :current-page="pageIndex"
                             :page-sizes="[10,20,50,100]"
                             :page-size="pageSize"
                             :total="totalPage"
                             layout="prev, pager, next" :pager-count="5" small>
                <!--layout="prev, pager, next, jumper, sizes, total" background>-->
              </el-pagination>
            </div>
          </div>
        </el-main>
      </el-container>
    </el-form>

    <span slot="footer" class="dialog-footer">
      <el-button v-if="openType !== 'see'" type="primary" @click="send()" size="medium"></el-button>
      <el-button v-if="openType !== 'see'" @click="visible = false"></el-button>
      <el-button v-if="openType === 'see'" @click="visible = false"></el-button>
    </span>
  </el-dialog>
</template>
<script type="text/ecmascript-6">
import API from '@/api'
import '../common/icon/iconfont.css'
import {Currency} from '@/utils/validate' // 
import TableOne from './table-one'
import TableTwo from './table-two'
import TableThree from './table-three'
import TableFour from './table-four'
export default {
  components: {
    TableOne,
    TableTwo,
    TableThree,
    TableFour
  },
  // Error in callback for watcher "$route": "TypeError: Cannot read property 'id' of undefined"
  // 
  watch: {
    'cutOut': function (newval, oldval) {
      if (newval === true) {
        this.chenxiHeight = 0
        setTimeout(() => {
          let youHeight = getComputedStyle(document.getElementsByClassName('el-main')[0]).height
          let zuoHeight = getComputedStyle(document.getElementById('leftHeightPatient')).height
          this.chenxiHeight = parseInt(youHeight) - parseInt(zuoHeight)
        }, 450)
      } else {
        setTimeout(() => {
          let youHeight = getComputedStyle(document.getElementsByClassName('el-main')[0]).height
          let zuoHeight = getComputedStyle(document.getElementById('leftHeightPatient')).height
          this.chenxiHeight = parseInt(youHeight) - parseInt(zuoHeight)
        }, 450)
      }
    },
    'dataForm.SpellName': function (val, oldval) { // SpellName
      if (val === '') {
        this.litterArr.forEach(item => {
          item.isActive = false
        })
      }
      this.getStoreCategorytypeStock()
    }
  },
  data () {
    return {
      cutOut: true, // 
      litterArr: [
        {content: 'A', isActive: false}, {content: 'N', isActive: false},
        {content: 'B', isActive: false}, {content: 'O', isActive: false},
        {content: 'C', isActive: false}, {content: 'P', isActive: false},
        {content: 'D', isActive: false}, {content: 'Q', isActive: false},
        {content: 'E', isActive: false}, {content: 'R', isActive: false},
        {content: 'F', isActive: false}, {content: 'S', isActive: false},
        {content: 'G', isActive: false}, {content: 'T', isActive: false},
        {content: 'H', isActive: false}, {content: 'U', isActive: false},
        {content: 'I', isActive: false}, {content: 'V', isActive: false},
        {content: 'J', isActive: false}, {content: 'W', isActive: false},
        {content: 'K', isActive: false}, {content: 'X', isActive: false},
        {content: 'L', isActive: false}, {content: 'Y', isActive: false},
        {content: 'M', isActive: false}, {content: 'Z', isActive: false}],
      drugsCategoryArr: [], // 
      drugsCategoryArrCopy: [], // 
      oldTabsName: '1001', // 
      activeName: '1001',
      dataList: [],
      leftTable: 'TableOne', //  
      leftTableData: [], // 
      rightUl: '', // 
      rightUlData: [], // 

      dataListLoading: false, // 
      chenxiHeight: 345, // 
      pageIndex: 1,
      pageSize: 30, // 50 
      totalPage: 1,
      dataForm: {
        SpellName: '',
        agreementRecipelId: '', // id

        // StoreId: '', // 
        // AccountId: '', // 
        oldCategoryOne: '1', // 
        CategoryOne: '1', // 

        MainCure: '', // 
        Effect: '', // 
        Explain: '', // 
        PrescriptionName: '', // 
        DrugRate: '', // 
        OrderType: '41' // 
      },
      dataRule: {
        UserName: Currency(''),
        DrugRate: Currency('')
      },
      visible: false,
      openType: 'add' // add see edit
    }
  },
  mounted () {
    var youHeight = getComputedStyle(document.getElementsByClassName('el-main')[0]).height
    var zuoHeight = getComputedStyle(document.getElementById('leftHeightPatient')).height
    this.chenxiHeight = parseInt(youHeight) - parseInt(zuoHeight)
  },
  methods: {
    // 
    changeStoreData (choseStoreId, isMultiple) { // 
      // if (isMultiple === false) { this.dataForm.StoreId = choseStoreId }
    },
    handleClose () {
      this.visible = false
      this.dataForm = {
        SpellName: '',
        agreementRecipelId: '', // id
        // AccountId: '', // 
        oldCategoryOne: '1', // 
        CategoryOne: '1', // 
        MainCure: '', // 
        Effect: '', // 
        Explain: '', // 
        PrescriptionName: '', // 
        DrugRate: '', // 
        OrderType: '41' // 
        // Status: '3,5,6', // -11 2 3- 56  -2
      }
      this.leftTableData = []
      this.oldTabsName = '1001'
      this.activeName = '1001'
    },

    // 
    filterCategory (arr) {
      var brr = []
      for (let i = 0; i < arr.length; i++) {
        this.drugsCategoryArrCopy.forEach(item => {
          if (item.id === arr[i]) { brr.push(item) }
        })
      }
      this.drugsCategoryArr = brr
    },

    pageInit (agreementRecipelId, type) { // type 'add see edit'
      this.visible = true
      this.dataListLoading = true
      this.openType = type
      // this.dataForm.AccountId = AccountId

      // 
      API.drugs.getDrugsCategory().then(result => {
        if (result.code === '0000') {
          this.drugsCategoryArr = result.data.filter((item, ind) => {
            return ind > 0
          })
        }
        this.drugsCategoryArrCopy = this.drugsCategoryArr
      })

      if (agreementRecipelId) { //   
        this.dataForm.agreementRecipelId = agreementRecipelId
        API.register.getRegisterInfo({id: this.dataForm.agreementRecipelId}).then(result => {
          if (result.code === '0000') {
            console.log(result.data)
            this.dataForm.SpellName = ''
            this.dataForm.agreementRecipelId = result.data.Id // id
            // StoreId: '' // 
            // this.dataForm.AccountId = result.data.AccountId // 
            this.dataForm.MainCure = result.data.MainCure // 
            this.dataForm.Effect = result.data.Effect // 
            this.dataForm.Explain = result.data.Explain // 
            this.dataForm.PrescriptionName = result.data.PrescriptionName // 
            this.dataForm.DrugRate = result.data.DrugRate // 
            this.dataForm.oldCategoryOne = String(result.data.CategoryOne) //  12345
            this.dataForm.CategoryOne = String(result.data.CategoryOne) //  12345
            switch (this.dataForm.CategoryOne) { // [1001100210031004]
              case '1':
                this.filterCategory(['1001', '1002', '1003', '1004']) // 
                break
              case '3':
                this.filterCategory(['1004']) // 
                break
              case '4':
                this.filterCategory(['1001', '1003']) // 
                break
              case '5':
                this.filterCategory(['1001', '1003'])
                break
              case '2':
                this.filterCategory(['1001', '1002', '1003', '1004']) // 
                break
            }
          }
          // table 
          this.leftTableData = result.data.SaleOrderItems.map(item => {
            item.CategoryName = item.CategoryName.substring(4)
            item.Id = item.ProductId // bug
            item.myNum = item.Quantity
            item.ShowName = item.ProductName
            item.Code = item.ProductCode
            return item
          })

          this.getStoreCategorytypeStock()

          // 
          this.oldTabsName = String(result.data.SaleOrderItems[0].CategoryId)
          this.activeName = String(result.data.SaleOrderItems[0].CategoryId)
          // console.log(this.dataForm.agreementRecipelId)
          // console.log(this.leftTableData)
        })
      } else {
        // 
        this.getStoreCategorytypeStock() // return false
      }

      // 0[]
      // if (this.$route.query.MobilePhone === '0') {
      //   return false
      // }
      this.dataListLoading = false
    },

    //    
    getStoreCategorytypeStock () {
      API.drugs.getDrugsList({
        Name: '',
        PageIndex: this.pageIndex,
        PageSize: this.pageSize,
        IsPaging: true,
        SpellName: this.dataForm.SpellName,
        CategoryId: this.activeName, // tabsid 1001
        StoreId: this.$store.getters.getAccountCurrentHandleStore, // id!!!
        CodeOrBarCode: '' // 
      }).then(result => {
        if (result.code === '0000' && result.data.length > 0) {
          this.dataList = result.data // dataList
          this.rightUlData = result.data
          this.totalPage = result.total
        } else {
          this.dataList = []
          this.rightUlData = []
          this.totalPage = 0
          this.$message({ message: `${result.message}`, type: 'warning', duration: 3000 })
        }
        // this.dataListLoading = false
      })
    },

    // 1.
    addDrugs (row) {
      console.log(row)
      if (this.leftTableData.some(item => item.Code === row.Code)) {
        // this.$alert(`[${row.ShowName}] `, '', {
        this.$alert(`<b style="color: #409EFF;font-size: 14px;font-weight: 500">[${row.ShowName}]</b> `, '', {
          confirmButtonText: '',
          dangerouslyUseHTMLString: true,
          closeOnClickModal: true,
          closeOnPressEscape: true
        })
        return false
      }
      row.myNum = 1 // myNum
      this.leftTableData.push(row)
    },
    // 2.table
    delDrugs (row) {
      console.log(this.leftTableData, row)
      this.leftTableData.forEach((item, i) => {
        if (item.Code === row.Code) {
          this.leftTableData.splice(i, 1)
          return false
        }
      })
    },
    // 3.myNum
    consoleTable () {
      this.leftTableData.push() // myNumtablepush()push()
    },

    // 
    handleChange (leftLab) {
      console.log(leftLab)
      if (this.leftTableData.length === 0) { // 
        switch (leftLab) { // [1001100210031004]
          case '1':
            this.filterCategory(['1001', '1002', '1003', '1004']) // 
            break
          case '3':
            this.filterCategory(['1004']) // 
            break
          case '4':
            this.filterCategory(['1001', '1003']) // 
            break
          case '5':
            this.filterCategory(['1001', '1003'])
            break
          case '2':
            this.filterCategory(['1001', '1002', '1003', '1004']) // 
            break
        }
        this.activeName = this.drugsCategoryArr[0].id
        switch (this.activeName) {
          case '1001':
            this.leftTable = 'TableOne'
            this.rightUl = 'ul-one'
            break
          case '1002':
            this.leftTable = 'TableTwo'
            this.rightUl = 'ul-one'
            break
          case '1003':
            this.leftTable = 'TableThree'
            this.rightUl = 'ul-one'
            break
          case '1004':
            this.leftTable = 'TableFour'
            this.rightUl = 'ul-one'
            break
        }
        this.dataForm.oldCategoryOne = leftLab
        this.oldTabsName = this.activeName
        this.getStoreCategorytypeStock()
      } else { // tabs
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          switch (leftLab) { // [1001100210031004]
            case '1':
              this.filterCategory(['1001', '1002', '1003', '1004']) // 
              break
            case '3':
              this.filterCategory(['1004']) // 
              break
            case '4':
              this.filterCategory(['1001', '1003']) // 
              break
            case '5':
              this.filterCategory(['1001', '1003'])
              break
            case '2':
              this.filterCategory(['1001', '1002', '1003', '1004']) // 
              break
          }
          this.activeName = this.drugsCategoryArr[0].id
          switch (this.activeName) {
            case '1001':
              this.leftTable = 'TableOne'
              this.rightUl = 'ul-one'
              break
            case '1002':
              this.leftTable = 'TableTwo'
              this.rightUl = 'ul-one'
              break
            case '1003':
              this.leftTable = 'TableThree'
              this.rightUl = 'ul-one'
              break
            case '1004':
              this.leftTable = 'TableFour'
              this.rightUl = 'ul-one'
              break
          }
          this.dataForm.oldCategoryOne = leftLab
          this.oldTabsName = this.activeName
          this.getStoreCategorytypeStock()
          this.leftTableData = [] // table
        }).catch(() => {
          this.dataForm.CategoryOne = this.dataForm.oldCategoryOne
          this.activeName = this.oldTabsName // oldTabsNametableultableData
        })
      }
      // console.log(this.dataForm.CategoryOne, this.dataForm.oldCategoryOne, this.activeName, this.oldTabsName)
      // this.rightCategoryChangeClick(this.activeName) // 
    },

    handleClick (tab, event) {
      if (this.leftTableData.length === 0) { // 
        switch (tab.name) {
          case '1001':
            this.leftTable = 'TableOne'
            this.rightUl = 'ul-one'
            break
          case '1002':
            this.leftTable = 'TableTwo'
            this.rightUl = 'ul-one'
            break
          case '1003':
            this.leftTable = 'TableThree'
            this.rightUl = 'ul-one'
            break
          case '1004':
            this.leftTable = 'TableFour'
            this.rightUl = 'ul-one'
            break
        }
        this.oldTabsName = this.activeName
        this.getStoreCategorytypeStock()
      } else { // tabs
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          switch (tab.name) {
            case '1001':
              this.leftTable = 'TableOne'
              this.rightUl = 'ul-one'
              break
            case '1002':
              this.leftTable = 'TableTwo'
              this.rightUl = 'ul-one'
              break
            case '1003':
              this.leftTable = 'TableThree'
              this.rightUl = 'ul-one'
              break
            case '1004':
              this.leftTable = 'TableFour'
              this.rightUl = 'ul-one'
              break
          }
          this.oldTabsName = this.activeName
          this.getStoreCategorytypeStock()
          this.leftTableData = [] // table
        }).catch(() => {
          this.activeName = this.oldTabsName // oldTabsNametableultableData
        })
      }
    },
    // A B C D...
    activeLitter (txt, ind) {
      this.dataForm.SpellName = txt
      this.litterArr.forEach((item, indx) => {
        if (indx === ind) {
          this.litterArr[indx].isActive = true
        } else {
          this.litterArr[indx].isActive = false
        }
      })
    },

    //  ()
    sizeChangeHandle (val) {
      this.pageSize = val
      this.pageIndex = 1
      this.getStoreCategorytypeStock()
    },
    // 
    currentChangeHandle (val) {
      this.pageIndex = val
      this.getStoreCategorytypeStock()
    },
    // 
    send () {
      // vuex

      if (this.leftTableData.length === 0) {
        this.$alert(``, '', {
          confirmButtonText: '',
          dangerouslyUseHTMLString: true,
          closeOnClickModal: true,
          closeOnPressEscape: true
        })
        return false
      }
      console.log(this.leftTableData)
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          // 
          var paramsEdit = {
            id: this.dataForm.agreementRecipelId, // id
            // StoreId: this.$store.getters.getAccountCurrentHandleStore, // 
            // AccountId: this.dataForm.AccountId, // id
            // OrderType: '41', // 

            MainCure: this.dataForm.MainCure, // 
            Effect: this.dataForm.Effect, // 
            Explain: this.dataForm.Explain, // 
            PrescriptionName: this.dataForm.PrescriptionName, // 
            DrugRate: this.dataForm.DrugRate, // 
            ItemsJson: JSON.stringify(this.leftTableData.map(item => {
              var obj = {
                ProductId: item.Id,
                ProductCode: item.Code,
                ProductName: item.ShowName,
                CostPrice: item.CostPrice,
                SalePrice: item.SalePrice,
                RealPrice: item.SalePrice,
                Quantity: item.myNum,
                SupplierId: 0,
                SupplierCode: 0 // 
              }
              return obj
            })),
            CategoryOne: this.dataForm.CategoryOne //  12345
          }

          // 
          var paramsCreate = {
            StoreId: this.$store.getters.getAccountCurrentHandleStore, // 
            // AccountId: this.dataForm.AccountId, // id
            OrderType: '41', // 

            MainCure: this.dataForm.MainCure, // 
            Effect: this.dataForm.Effect, // 
            Explain: this.dataForm.Explain, // 
            PrescriptionName: this.dataForm.PrescriptionName, // 
            DrugRate: this.dataForm.DrugRate, // 
            ItemsJson: JSON.stringify(this.leftTableData.map(item => {
              var obj = {
                ProductId: item.Id,
                ProductCode: item.Code,
                ProductName: item.ShowName,
                CostPrice: item.CostPrice,
                SalePrice: item.SalePrice,
                RealPrice: item.SalePrice,
                Quantity: item.myNum,
                SupplierId: 0,
                SupplierCode: 0 // 
              }
              return obj
            })),
            CategoryOne: this.dataForm.CategoryOne //  1 2 3 4 5
          }
          console.log(paramsEdit) //  = 
          console.log(paramsCreate) //  = 
          var tick = this.openType === 'add' ? API.register.registerSubmit(paramsCreate) : API.register.sendRecipelToEdit(paramsEdit)
          tick.then((data) => {
            if (data.code === '0000') {
              this.$message({
                message: `${''}`,
                type: 'success',
                duration: 1500,
                onClose: () => {
                  this.visible = false
                  this.$emit('refreshDataList')
                }
              })
            } else {
              this.$message.error(data.message)
            }
          })
        }
      })
    }

  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .recipelAgeUnit /deep/ {
    display: inline-block;
    .el-input__inner {
      padding: 0 5px;
    }
  }
  .doctor-recipel /deep/ {
    background-color: #fff;
    margin: 0 10px;
    .el-radio-button--mini .el-radio-button__inner {
      padding: 7px 9px;
    }
    .el-main {position: relative;}
    .el-main::-webkit-scrollbar { width: 0px; }

    .el-table__body-wrapper.is-scrolling-none::-webkit-scrollbar, .el-table__body-wrapper.is-scrolling-left::-webkit-scrollbar, .ownScrollbar::-webkit-scrollbar {
      width: 7px;
      /*z-index: -1;*/
    }
    .ownScrollbar:hover::-webkit-scrollbar {
      /*display: block;*/
    }
    .el-table__body-wrapper.is-scrolling-none::-webkit-scrollbar-thumb, .el-table__body-wrapper.is-scrolling-left::-webkit-scrollbar-thumb, .ownScrollbar::-webkit-scrollbar-thumb {
      border-radius: 3px;
      box-shadow: inset 0 0 5px rgba(0,0,0,0.1);
      background-color: #eee;
    }
    .el-table__body-wrapper.is-scrolling-none::-webkit-scrollbar-track, .el-table__body-wrapper.is-scrolling-left::-webkit-scrollbar-track, .ownScrollbar::-webkit-scrollbar-track {
      border-radius: 0;
      box-shadow: inset 0 0 5px rgba(0,0,0,0);
      background-color: rgba(0,0,0,0);
    }

    /*rightUlStyle div*/
    .rightUlStyle {
      position: relative;
      width: 80%;
      min-width: 190px;
      ul {
        width: 100%;
        min-height: 480px; // 
        max-height: 480px;
        overflow-y: scroll;
        background-color: #fff;
        box-shadow: 0 0 10px 1px #f1f2f7 inset;
        li {
          min-width: 180px;
          height: 30px;
          line-height: 30px;
          padding-left: 5px;
          font-weight: 600;
          font-size: 15px;
        }
      }
      ol {
        position: absolute;
        top: 0;
        right: -56px;
        color: #050505;
        font-size: 14px;
        font-weight: 700;
        width: 51px;
        text-align: center;
        border-left: 1px solid #dedede;
        border-top: 1px solid #dedede;
        li {
          float: left;
          width: 25px;
          height: 25px;
          line-height: 25px;
          cursor: pointer;
          border-right: 1px solid #dedede;
          border-bottom: 1px solid #dedede;
          &.isActive {
            background-color: #409EFF;
            color: #fff;
          }
        }
      }
    }
    .rightPageCounterContainer {
      text-align: center;
      .rightPageCounter {
        display: inline-block;
      }
    }
    .el-pagination.el-pagination--small {padding: 0 5px}
  }
  /*cxform22px*/
  .doctor-recipel /deep/ {
    .el-form-item {
      margin-bottom: 7px;
    }
    .el-dialog__body {
      padding-top: 10px;
    }
    /*35*/
    .el-table--medium th, .el-table--medium td, & .el-table th, & .el-table td,
    .el-table--medium th, .el-table--medium td, & .el-table th, & .el-table td {
      padding: 0 !important;
    }
    /*& /deep/ .el-tabs__content {background-color: #F0F0F0}*/
    .purchaseListRow {
      color: #606266;
      td {padding: 0;}
      td .cell{
        height: 35px;
        line-height: 35px;
      }
    }
    /*cutOutshow*/
    .v-enter, .v-leave-to {opacity: 0}
    .v-enter-active, .v-leave-active {transition: all 0.4s ease}
    .v-enter-to, .v-leave {opacity: 1}
  }
</style>



