<template>
  <el-dialog
    v-dialogDrag
    :title="''" :width="isAddActive === false ? '1000px' : '85%'"
    :close-on-click-modal="false"
    :visible.sync="visible" @close="handleClose">
    <!--<el-col><div style="border-top: 1px dashed #ccc;padding-top: 10px;font-weight: 900"></div></el-col>-->
    <el-container><!-- display: none -->
      <el-aside :width="isAddActive === false ? '100%' : '60%'">
        <div class="ownScrollbar" style="min-height: 500px;max-height: 500px;overflow-y: scroll;">

          <!---->
          <el-row>
            <el-col style="padding: 0 50px">
              <el-steps :active="stepActive" process-status="success" finish-status="success" align-center>
                <el-step title="()" description=""></el-step>
                <el-step title="" description=""></el-step>
                <el-step title="" description=""></el-step>
              </el-steps>
            </el-col>
          </el-row>
          <!---->
          <el-row>
            <el-col :span="8"><p><span v-if="dataList !== null">{{dataList.CreatedByName}}</span></p></el-col>
            <el-col :span="8"><p><span v-if="dataList !== null">{{dataList.SupplierName}}</span></p></el-col>
            <el-col :span="8">
              <p style="width: 190px;">
                <span v-if="dataList !== null" style="display: inline-block; vertical-align: bottom; width: 134px; overflow: hidden; text-overflow:ellipsis;white-space:nowrap;">{{dataList.StoreName}}</span>
              </p>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8"><p><span v-if="dataList !== null">{{dataList.Buyer}}</span></p></el-col>
            <el-col :span="8"><p><span v-if="dataList !== null">{{dataList.Phone}}</span></p></el-col>
            <el-col :span="8"><p><span v-if="dataList !== null">{{dataList.Address}}</span></p></el-col>
          </el-row>
          <el-row>
            <el-col :span="8"><p><span v-if="dataList !== null">{{dataList.CreatedTime | myDateFilter('yyyy-MM-dd hh:mm:ss')}}</span></p></el-col>
            <el-col :span="15"><p><span v-if="dataList !== null">{{dataList.Remark}}</span></p></el-col>
          </el-row>
          <el-row>
            <el-col><div style="padding-top: 5px;font-weight: 900;color: #1CA579"><b v-text="categoryName"></b></div></el-col>
          </el-row>

          <!---->
          <el-table  v-if="dataList !== null" :key="Math.random()"
                     :data="dataList.Items"
                     stripe
                     v-loading="dataListLoading"
                     row-class-name="purchaseListRow"
                     :header-cell-style="$cxObj.tableHeaderStyle40px"
                     style="width: 100%;">
            <!--<el-table-column prop="Id" align="center" label="ID" width="50"></el-table-column>-->
            <el-table-column type="index" align="center" label="" fixed width="50"></el-table-column>
            <el-table-column prop="ProductId" align="center" label="" fixed min-width="50"></el-table-column>
            <el-table-column prop="CategoryName" header-align="center" align="center" label="" fixed min-width="70" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="ProductName" header-align="center" align="center" label="" fixed min-width="80" :show-overflow-tooltip="true"></el-table-column>

            <!---->
            <el-table-column v-if="editType === 'A'" :key="Math.random()" label="" header-align="center" :align="$store.state.common.align" width="110">
              <template slot-scope="scope">
                <el-input-number v-model="scope.row.Quantity" :step="1" :min="1" :max="1000" size="mini" controls-position="right" style="width: 95px"></el-input-number>
              </template>
            </el-table-column>
            <el-table-column v-else prop="Quantity" :key="Math.random()" label="" header-align="center" align="center"></el-table-column>

            <!---->
            <el-table-column prop="LastCostPirce" header-align="center" align="center" label=""></el-table-column>

            <!--!!!-->
            <el-table-column v-if="editType === 'A'" :key="Math.random()" label="" header-align="center" :align="$store.state.common.align" width="110">
              <template slot-scope="scope">
                <el-input-number v-model="scope.row.CostPrice" :precision="2" :step="0.01" :min="0.01" :max="1000" size="mini" controls-position="right" style="width: 100px"></el-input-number>
              </template>
            </el-table-column>
            <el-table-column v-else prop="CostPrice" header-align="center" label="" align="center"></el-table-column>

            <!---->
            <!--<el-table-column v-if="editType === 'B'" :key="Math.random()" label="" prop="" header-align="center" :align="$store.state.common.align" width="110">-->
              <!--<template slot-scope="scope">-->
                <!--<el-input-number v-model="scope.row.StoreSalePrice" :precision="2" :step="0.01" :min="0.01" :max="1000" size="mini" controls-position="right" style="width: 100px"></el-input-number>-->
              <!--</template>-->
            <!--</el-table-column>-->
            <!--<el-table-column v-if="editType !== 'B'" prop="StoreSalePrice" label="" header-align="center" align="center"></el-table-column>-->

            <!-- 2019.07.01-->
            <el-table-column v-if="editType === 'B'" :key="Math.random()" label="" header-align="center" :align="$store.state.common.align" width="140">
              <template slot-scope="scope">
                <el-date-picker
                  v-model="scope.row.ProductionDate"
                  align="right"
                  type="date" size="mini"
                  placeholder=""
                  :picker-options="pickerOptions_0" style="width: 128px">
                </el-date-picker>
              </template>
            </el-table-column>
            <el-table-column v-if="dataList.Status !== 1 && dataList.Status !== -1 && editType !== 'B'" :key="Math.random()" label="" header-align="center" align="center">
              <template slot-scope="scope">
                <span v-if="scope.row.ProductionDate === null"></span>
                <span v-else>{{scope.row.ProductionDate | myDateFilter('yyyy-MM-dd')}}</span>
              </template>
            </el-table-column>

            <!-- 2019.07.01-->
            <el-table-column v-if="editType === 'B'" :key="Math.random()" label="" header-align="center" :align="$store.state.common.align" width="140">
              <template slot-scope="scope">
                <el-date-picker
                  v-model="scope.row.ExpiryDate" @focus="ExpiryDateFocus(scope.row.ProductionDate)"
                  align="right"
                  type="date" size="mini"
                  placeholder=""
                  :picker-options="pickerOptions_1" style="width: 128px">
                </el-date-picker>
              </template>
            </el-table-column>
            <el-table-column v-if="dataList.Status !== 1 && dataList.Status !== -1 && editType !== 'B'" :key="Math.random()" label="" header-align="center" align="center">
              <template slot-scope="scope">
                <span v-if="scope.row.ExpiryDate === null"></span>
                <span v-else>{{scope.row.ExpiryDate | myDateFilter('yyyy-MM-dd')}}</span>
              </template>
            </el-table-column>


            <!---->
            <!--<el-table-column prop="Amount" header-align="center" align="center" label=""></el-table-column>-->
            <el-table-column v-if="editType === 'B'" :key="Math.random()" prop="" header-align="center" :align="$store.state.common.align" label="" width="150">
              <template slot-scope="scope">
                <el-input v-model="scope.row.ProductBatchNo" placeholder="" size="mini"></el-input>
                <!--<el-input-number v-model="scope.row.ProductBatchNo" :step="1" :min="0" :max="10000" size="mini" controls-position="right"></el-input-number>-->
              </template>
            </el-table-column>
            <el-table-column v-if="dataList.Status === 10" prop="ProductBatchNo" header-align="center" align="center" label=""></el-table-column>

            <el-table-column v-if="editType === 'A'" prop="" label="" header-align="center" align="center">
              <template slot-scope="scope">
                <el-button type="text" @click="leftRemove(scope.row.ProductId)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <p style="font-size: 16px;height: 40px;line-height: 40px">
            <span style="color: #e4393c">{{OrderTotalPrice.toFixed(2)}}</span>
          </p>
        </div>

        <!--24.109.31cm-->
        <!-- -->
        <!-- -->
        <!-- --><!-- style="display: none" -->
        <div id="chenxiPrint" v-if="dataList !== null" style="display: none">
          <div v-for="index in pages" :key="index" style="font-size: 12px; width: 80%"> <!--  -->
            <div>
              <div style="text-align: center;font-size: 18px;font-weight: 900"></div>
              <div>
                <div style="display: inline-block;width: 30%;text-align: left">{{dataList.SupplierName}}</div>
                <div style="display: inline-block;width: 25%;text-align: right">{{dataList.CreatedTime | myDateFilter('yyyy-MM-dd')}}</div>
                <div style="display: inline-block;width: 40%;text-align: right">{{dataList.Code}}</div>
              </div>
            </div>
            <table border="1" style="border-collapse: collapse;border-sizing: border-box" width="100%">
              <tbody style="text-align: center;line-height: 16px">
              <tr style="font-size: 12px">
                <td height="16"><p></p></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>

                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr  style="font-size: 12px" v-for="(item, ind) in dataList.Items.slice((index - 1) * 10, (index * 10))" :key="item.ProductCode">
                <td width="50" height="16">{{ind + (index - 1) * 10 + 1}}</td>
                <td width="70">{{item.ProductCode}}</td>
                <td width="100" align="center"><p style="width: 100px;margin: 0 5px;white-space: nowrap;overflow: hidden;">{{item.ProductName}}</p></td>
                <td width="70" align="center"><p style="width: 70px;margin: 0 5px;white-space: nowrap;overflow: hidden;">{{item.Specification}}</p></td>
                <td width="100" align="center"><p style="width: 100px;margin: 0 5px;white-space: nowrap;overflow: hidden;">{{dataList.SupplierName}}</p></td>

                <td width="50">{{item.Unit}}</td>
                <td width="70">{{item.Quantity}}</td>
                <td width="70">{{item.CostPrice.toFixed(2)}}</td>
                <td width="70">{{(item.Quantity * item.CostPrice).toFixed(2)}}</td>
                <td width="100" align="center"><p style="width: 100px;white-space: nowrap;overflow: hidden;">{{item.ProductBatchNo}}</p></td>
              </tr>
              <tr style="font-size: 12px">
                <td colspan="2" align="center" height="16"> {{dataList.Items.length}} </td>
                <td colspan="6" align="left" style="padding: 0 10px;font-weight: bold">{{index === pages ? '' + sumCountAll(dataList.Items) +
                  '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;' + sumCountChinese(sumCountAll(dataList.Items)) : ''}}</td>
                <td colspan="2" align="center">{{sumCount(index)}}</td>
              </tr>
              </tbody>
            </table>
            <div>
              <div style="display: inline-block;width: 30%;text-align: left">{{dataList.CreatedByName}}</div>
              <div style="display: inline-block;width: 25%;text-align: right">{{$store.getters.getAccountLoginInfoAll.NickName}}</div>
              <div style="display: inline-block;width: 40%;text-align: right;"> {{index}}  {{pages}} </div>
            </div>
            <div style="page-break-after: always"></div>
          </div>
        </div>

      </el-aside>

      <!---->
      <el-main v-if="isAddActive" style="padding: 0 0 0 5px !important; border-left: 1px solid #E6E6E6">
        <!--<el-form :model="dataForm" :rules="dataRule" ref="dataForm" label-width="0" :inline="true">-->
        <el-form class="purchaseListInfo" :model="dataForm" ref="dataForm" label-width="0" :inline="true">
          <el-form-item label="" prop="SpellName">
            <el-input v-model="dataForm.SpellName" placeholder="" size="mini" clearable>
              <el-button slot="append" icon="el-icon-search" @click="drugsSearch()"></el-button>
            </el-input>
          </el-form-item>
          <!--@selection-change="selectionChangeHandle"-->
          <!--<el-table-column type="selection" header-align="center" :align="$store.state.common.align" width="50"></el-table-column>-->
          <el-table
            :data="dataListAdd" ref="tableChild" :key="Math.random()"
            stripe
            :height="450"
            v-loading="dataListLoading"
            :header-cell-style="$cxObj.tableHeaderStyle40px"
            row-class-name="purchaseTableRowClass"
            style="width: 100%;">
            <el-table-column :align="$store.state.common.align" type="index" label="" width="50px"></el-table-column>
            <el-table-column prop="Code" header-align="center" :align="$store.state.common.align" width="90" label="" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="CategoryName" header-align="center" :align="$store.state.common.align" width="" label="" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="ShowName" header-align="center" :align="$store.state.common.align" label="" width="70" :show-overflow-tooltip="true"></el-table-column>
            <!--<el-table-column prop="Specification" header-align="center" :align="$store.state.common.align" label="" :show-overflow-tooltip="true"></el-table-column>-->
            <!--<el-table-column prop="Unit" header-align="center" :align="$store.state.common.align" label=""></el-table-column>-->
            <!--<el-table-column prop="RedLine" header-align="center" :align="$store.state.common.align" label="" :show-overflow-tooltip="true"></el-table-column>-->
            <el-table-column prop="Quantity" header-align="center" :align="$store.state.common.align" label="" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="" label="" width="50" header-align="center" align="center">
              <template slot-scope="scope">
                <el-button type="text" @click="rightAdd(scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-form>
      </el-main>
    </el-container>

    <!--footer-->
    <span v-if="editType !== ''" slot="footer" class="dialog-footer">
      <el-button @click="visible = false"></el-button>
      <el-button v-if="editType === 'A'" type="primary" @click="dataFormAdd()"></el-button>
      <el-button v-if="editType === 'A'" type="primary" @click="dataFormSubmitA()"></el-button>
      <el-button v-else-if="editType === 'B'" type="primary" @click="dataFormSubmitB()"></el-button>
    </span>
    <span v-else-if="stepActive === 0" slot="footer" class="dialog-footer">
      <el-button @click="excelExports('purchase')">Excel()</el-button>
    </span>
    <span v-else-if="stepActive === 2" slot="footer" class="dialog-footer">
      <el-button @click="chenxiPrint()"></el-button>
      <el-button @click="excelExports('caiwu')">Excel()</el-button>
    </span>
  </el-dialog>
</template>
<script type="text/ecmascript-6">
import API from '@/api'
import {Currency, Letter, NumberInt, NumberFloat} from '../../utils/validate'
// import {treeDataTranslate} from '@/utils'
export default {
  components: {
  },
  watch: {
    'dataList.Items': function (val, oldval) {
      this.pages = parseInt(val.length / 10 + 1) // 
      if (val.length !== 0 && (val.length % 10) === 0) {
        this.pages-- // 10
      }
    }
  },
  data () {
    return {
      stepActive: -1,
      visible: false,
      dataListLoading: false, // 
      Id: '',
      dataForm: {
        SpellName: ''
      },
      dataList: null,
      editType: '', // AB
      categoryName: '',
      categoryId: '',

      isAddActive: false,
      dataListAdd: [],
      OrderTotalPrice: 0, // 
      pickerOptions_0: {
        disabledDate (time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [{ // 
          text: '1',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 24 * 30 * 1000)
            picker.$emit('pick', date)
          }
        }, {
          text: '6',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 24 * 180 * 1000)
            picker.$emit('pick', date)
          }
        }, {
          text: '1',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 24 * 365 * 1000)
            picker.$emit('pick', date)
          }
        }]
      },
      pickerOptions_1: null,
      pages: 1 //  
    }
  },
  methods: {
    // 
    sumCount (n) {
      return this.dataList.Items.slice((n - 1) * 10, n * 10).map(item => item.CostPrice * item.Quantity).reduce((pren, nextm) => pren + nextm).toFixed(2)
    },
    // 
    sumCountAll (arr) {
      return arr.map(item => item.CostPrice * item.Quantity).reduce((pren, nextm) => pren + nextm).toFixed(2)
    },
    // 
    sumCountChinese (n) {
      if (!/^(0|[1-9]\d*)(\.\d+)?$/.test(n)) return ''
      var unit = ''
      var str = ''
      n += '00'
      var p = n.indexOf('.')
      if (p >= 0) n = n.substring(0, p) + n.substr(p + 1, 2)
      unit = unit.substr(unit.length - n.length)
      for (var i = 0; i < n.length; i++) str += ''.charAt(n.charAt(i)) + unit.charAt(i)
      return str.replace(/(|||)/g, '').replace(/()+/g, '').replace(/(||)/g, '$1').replace(/()|()/g, '$1$2').replace(/^?|/g, '').replace(/$/g, '')
    },

    ExpiryDateFocus (ProductionDate) {
      // 
      //  
      var preProductionDate = ProductionDate === null ? new Date() : ProductionDate
      this.pickerOptions_1 = { // 
        shortcuts: [{
          text: '3',
          onClick (picker) {
            const date = new Date(preProductionDate)
            date.setTime(date.getTime() + 3600 * 24 * 30 * 3 * 1000 + 1)
            picker.$emit('pick', date)
          }
        }, {
          text: '6',
          onClick (picker) {
            const date = new Date(preProductionDate)
            date.setTime(date.getTime() + 3600 * 24 * 30 * 6 * 1000 + 3600 * 24 * 3 * 1000)
            picker.$emit('pick', date)
          }
        }, {
          text: '112',
          onClick (picker) {
            const date = new Date(preProductionDate)
            date.setTime(date.getTime() + 3600 * 24 * 30 * 12 * 1000 + 3600 * 24 * 7 * 1000)
            picker.$emit('pick', date)
          }
        }, {
          text: '118',
          onClick (picker) {
            const date = new Date(preProductionDate)
            date.setTime(date.getTime() + 3600 * 24 * 30 * 18 * 1000 + 3600 * 24 * 10 * 1000)
            picker.$emit('pick', date)
          }
        }, {
          text: '2',
          onClick (picker) {
            const date = new Date(preProductionDate)
            date.setTime(date.getTime() + 3600 * 24 * 30 * 24 * 1000 + 3600 * 24 * 14 * 1000)
            picker.$emit('pick', date)
          }
        }, {
          text: '3',
          onClick (picker) {
            const date = new Date(preProductionDate)
            date.setTime(date.getTime() + 3600 * 24 * 30 * 36 * 1000 + 3600 * 24 * 21 * 1000)
            picker.$emit('pick', date)
          }
        }]
      }
    },

    comFunction () {
      //    
      API.drugs.getDrugsList({
        Name: '',
        PageIndex: 1,
        PageSize: 10000,
        IsPaging: 'false',
        SpellName: this.dataForm.SpellName,
        CategoryId: this.categoryId,
        StoreId: this.dataList.StoreId,
        CodeOrBarCode: ''
      }).then(result => {
        if (result.code === '0000') {
          this.dataListAdd = result.data
          this.dataList.Items.forEach(item0 => {
            this.dataListAdd = this.dataListAdd.filter(item => {
              return item0.ProductCode !== item.Code
            })
          })
        } else {
          this.$message({ message: '', type: 'warning', duration: 3000 })
        }
      })
    },
    //   
    dataFormAdd () {
      this.isAddActive = !this.isAddActive // '[]'
      if (this.isAddActive === false) {
        return false
      }
      this.comFunction()
    },
    leftRemove (ProductId) {
      this.dataList.Items = this.dataList.Items.filter(item => {
        return item.ProductId !== ProductId
      })
      if (this.isAddActive === true) {
        this.comFunction()
      }
    },
    rightAdd (row) {
      // el-table
      API.drugs.getDrugsList({name: '', PageIndex: 1, PageSize: 10000, IsPaging: 'false', SpellName: this.dataForm.SpellName, CategoryId: this.categoryId}).then(result => {
        if (result.code === '0000') {
          result.data.forEach(item => {
            if (item.Code === row.Code) {
              // this.dataList.Items.push(item) // tablerow
              this.dataList.Items.push({
                Id: '', // id CategoryName
                ProductId: item.Id, // need idid
                SapProductCode: item.Code, // need
                ProductCode: item.Code, // need
                ProductName: item.ShowName, // need
                Specification: null,
                Unit: 'g',
                CostPrice: item.SalePrice, // need
                SalePrice: 0,
                Preferential: 0,
                CategoryId: '',
                CategoryName: item.CategoryName, // need
                Quantity: item.Quantity, // need
                Amount: 0.33,
                ActualShipAmount: 0,
                ActualAmount: 0,
                ActualShipQuantity: 0,
                ActualQuantity: 0,
                BgColor: 'bg-danger',
                SupplierId: 0, // ???
                SupplierCode: null, // ???
                SupplierName: null,
                InventoryQuantity: 0,
                BatchNo: 0,
                ProductBatchNo: null,
                CargoFee: 0,
                Pictures: [
                  '/Content/AdminLTE/img/default-50x50.gif',
                  '/Content/AdminLTE/img/default-50x50.gif',
                  '/Content/AdminLTE/img/default-50x50.gif'
                ],
                StoreId: '', // ???,
                SapStoreCode: null,
                ShippedQuantityByInventory: 0,
                ShippedQuantityByFactory: 0,
                ActualOrderQuantity: 0
              })
              console.log(this.dataList.Items)
              return false
            }
          })
          this.$nextTick(() => {
            if (this.isAddActive === true) {
              this.comFunction()
            }
          })
        }
      })
    },
    // info
    init (id, type) {
      this.visible = true
      this.dataListLoading = true
      this.editType = type !== undefined ? type : '' // A B input
      if (id !== undefined) {
        API.purchase.getPurchaseInfo({id: id}).then(result => {
          if (result.code === '0000') {
            this.dataList = result.data
            console.log(result.data.Items)

            // 2019.06.01 
            this.dataList.Items.forEach(item => {
              this.OrderTotalPrice += item.Quantity * item.CostPrice
            })

            this.categoryId = this.dataList.Items[0].CategoryId
            this.categoryName = this.dataList.Items[0].CategoryName // 
            switch (result.data.Status) {
              case -1:
                this.stepActive = -1
                break
              case 1:
                this.stepActive = 0
                break
              case 4:
                this.stepActive = 1
                break
              case 10:
                this.stepActive = 2
                break
            }
            this.dataListLoading = false
          }
        })
      }
    },
    excelExports (excelType) {
      // var jsonData = []
      // var str = '' // 
      // if (excelType === 'purchase') { // 
      //   jsonData = this.dataList.Items.map(item => {
      //     return {
      //       dCategory: item.CategoryName,
      //       dCode: item.ProductCode,
      //       dName: item.ProductName,
      //       dNumber: item.Quantity,
      //       // dStoreSalePrice: item.StoreSalePrice,
      //       dInventoryQuantity: item.InventoryQuantity
      //     }
      //   })
      //   str = `<tr>
      //           <th colspan="6"><h3>()</h3></th>
      //         </tr>
      //         <tr>
      //           <td height="20"></td> <td height="20"></td>
      //           <td height="20"></td> <td height="20"></td>
      //           <td height="20"></td> <td height="20"></td>
      //         </tr>`
      // } else if (excelType === 'caiwu') { // 
      //   jsonData = this.dataList.Items.map(item => {
      //     return {
