<script type="text/ecmascript-6">
import API from '@/api'
import '../common/icon/iconfont.css'
import {calcAge, Currency} from '@/utils/validate'

export default {
  data () {
    return {
      visible: false,
      dataListLoading: false, // 
      addOrUpdateVisible: false, // 
      isAddActive: true,
      regMoney: /^\d+\.?\d{0,2}$/,

      registerAllData: '', // 
      oldDrugTotalAmount: '', // 100%
      mySumAmount: '', // dialog
      myFutureAmount: '', // 

      dataForm: {
        percentage: 100, // 
        WorkAmount: 0, // 
        DJAmount: 0, // 
        ExpressAmount: 0, // 
        OtherAmount: 0, // 

        PaymentWay: 1, // 
        OnlinePaymentWay: '', // 
        PayAmount: '', // _0
        OnlinePayAmount: 0, // _1

        give: '', // 
        PrescriptionName: '' // 
      },
      dataRule: {
        percentage: Currency(' '),
        WorkAmount: Currency(' '),
        DJAmount: Currency(' '),
        ExpressAmount: Currency(' '),
        OtherAmount: Currency(' '),

        PayAmount: Currency(' '),
        OnlinePayAmount: Currency(' ')
      },
      paymentAdd: false, // 
      optionsPaymentType: [ // 
        {label: '', value: 1, abled: true},
        {label: '', value: 2},
        {label: '', value: 3},
        {label: '', value: 4},
        {label: '', value: 5},
        {label: '', value: 6},
        {label: '', value: 7},
        {label: '', value: 8}]
    }
  },
  watch: {
    // 
    'dataForm.percentage': function (val, oldval) {
      this.registerAllData.DrugTotalAmount = Number(this.oldDrugTotalAmount * val / 100).toFixed(2)
    },
    // 
    'dataForm.PaymentWay': function (val, oldval) {
      this.optionsPaymentType.forEach(item => {
        if (val === item.value || this.dataForm.OnlinePaymentWay === item.value) {
          item.abled = true
        } else { item.abled = false }
      })
    },
    'dataForm.OnlinePaymentWay': function (val, oldval) {
      this.optionsPaymentType.forEach(item => {
        if (val === item.value || this.dataForm.PaymentWay === item.value) {
          item.abled = true
        } else { item.abled = false }
      })
    },
    // 
    'dataForm.PayAmount': function (val, oldval) {
      var sum = Number(val) + Number(this.dataForm.OnlinePayAmount)
      console.log(val, this.dataForm.OnlinePaymentWay, sum)
      if (Number(val) === 0 && String(val).length > 1) { // 000000 
        this.dataForm.PayAmount = ''
        return false
      }
      if (String(val).includes(' ').length) { // ____000 
        this.dataForm.PayAmount = ''
        return false
      }
      if (Number(val) !== 0 && !this.regMoney.test(val)) {
        this.$alert('! ', '', {
          confirmButtonText: '',
          callback: () => {
            this.dataForm.PayAmount = ''
            this.dataForm.give = ''
            return false
          }
        })
      }
      if (sum < this.myFutureAmount) { //     
        this.dataForm.give = ''
        return false
        // this.dataForm.give = Math.round((sum - this.registerAllData.TotalAmount) * 100) / 100 // 
      } else {
        this.dataForm.give = Math.round((sum - this.myFutureAmount) * 100) / 100 // 
      }
    },
    'dataForm.OnlinePayAmount': function (val, oldval) {
      var sum = Number(val) + Number(this.dataForm.PayAmount)
      console.log(this.dataForm.PaymentWay, val, sum)
      if (Number(val) === 0 && String(val).length > 1) { // 000000 
        this.dataForm.OnlinePayAmount = ''
        return false
      }
      if (String(val).includes(' ').length) { // ____000 
        this.dataForm.OnlinePayAmount = ''
        return false
      }
      if (Number(val) !== 0 && !this.regMoney.test(val)) {
        this.$alert('! ', '', {
          confirmButtonText: '',
          callback: () => {
            this.dataForm.OnlinePayAmount = ''
            this.dataForm.give = ''
            return false
          }
        })
      }
      if (sum < this.myFutureAmount) { //     
        this.dataForm.give = ''
        return false
        // this.dataForm.give = Math.round((sum - this.registerAllData.TotalAmount) * 100) / 100 // 
      } else {
        this.dataForm.give = Math.round((sum - this.myFutureAmount) * 100) / 100 // 
      }
    }
  },
  methods: {
    myComputedAttr () {
      // 
      this.mySumAmount = (this.registerAllData.RegisterAmount +
                          this.oldDrugTotalAmount * this.dataForm.percentage / 100 +
                          this.dataForm.WorkAmount + this.dataForm.DJAmount + this.dataForm.ExpressAmount + this.dataForm.OtherAmount).toFixed(2)
      // 
      if (this.registerAllData.RegisterStatus === 1) { //  + 
        this.myFutureAmount = this.mySumAmount
      } else if (this.registerAllData.RegisterStatus === 2) { // 
        this.myFutureAmount = Number(this.mySumAmount - this.registerAllData.RegisterAmount).toFixed(2)
      }
      this.dataForm.PayAmount = ''
      this.dataForm.OnlinePayAmount = 0
      this.dataForm.give = ''
    },
    // Id
    init (formId) {
      if (formId) {
        this.visible = true
        this.dataListLoading = true
        API.register.getRegisterInfo({id: formId}).then(result => {
          if (result.code === '0000') {
            result.data.BirthDate = calcAge(result.data.BirthDate)
            this.registerAllData = result.data
            this.oldDrugTotalAmount = result.data.DrugTotalAmount
            this.dataForm.WorkAmount = result.data.WorkAmount
            this.dataListLoading = false
            console.log('', result.data)

            this.myComputedAttr() // @change = myComputedAttr
          }
        })
      }
    },
    seeRecipelInfo () {
      this.isAddActive = !this.isAddActive // '[]'
    },
    handleClose () {
      this.isAddActive = true
      this.oldDrugTotalAmount = ''
      this.registerAllData = ''
      this.dataForm = {
        percentage: 100, // 
        WorkAmount: '', // 
        DJAmount: 0, // 
        ExpressAmount: 0, // 
        OtherAmount: 0, // 

        PaymentWay: 1, // 
        OnlinePaymentWay: '', // 
        PayAmount: '', // _0
        OnlinePayAmount: 0, // _1

        give: '', // 
        PrescriptionName: '' // 
      }
      this.paymentAdd = false
    },
    // 
    dataFormSubmit () {
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          // var  Actual = Number(this.dataForm.PayAmount) + Number(this.dataForm.OnlinePayAmount)
          if (this.paymentAdd === true && this.dataForm.OnlinePaymentWay === '') {
            this.$alert('! ', '', {confirmButtonText: ''})
            return false
          }
          if (this.paymentAdd === true) { // 
            if ((Number(this.dataForm.PayAmount) + Number(this.dataForm.OnlinePayAmount)) < Number(this.myFutureAmount)) {
              this.$alert('! ', '', {confirmButtonText: ''})
              return false
            }
            if (Number(this.dataForm.PayAmount) >= Number(this.myFutureAmount)) {
              this.dataForm.PayAmount = this.myFutureAmount
              this.dataForm.OnlinePayAmount = 0
            } else {
              this.dataForm.PayAmount = this.dataForm.PayAmount
              this.dataForm.OnlinePayAmount = Number(this.myFutureAmount - this.dataForm.PayAmount).toFixed(2)
            }
          } else { // 
            if (Number(this.dataForm.PayAmount) < Number(this.myFutureAmount)) {
              this.$alert('! ', '', {confirmButtonText: ''})
              return false
            }
            if (Number(this.dataForm.PayAmount) >= Number(this.myFutureAmount)) {
              this.dataForm.PayAmount = this.myFutureAmount
              this.dataForm.OnlinePaymentWay = '' // 
              this.dataForm.OnlinePayAmount = 0
            }
          }
          var  Actual = Number(this.dataForm.PayAmount) + Number(this.dataForm.OnlinePayAmount)
          var params = {
            id: this.registerAllData.Id,
            ActualAmount: Actual, // ???
            PaymentWay: this.dataForm.PaymentWay, // 
            OnlinePaymentWay: this.dataForm.OnlinePaymentWay, // 
            PayAmount: this.dataForm.PayAmount, // 
            OnlinePayAmount: this.dataForm.OnlinePayAmount, // 

            Discount: (this.dataForm.percentage / 100).toFixed(2), //  
            WorkAmount: this.dataForm.WorkAmount, // 
            DJAmount: this.dataForm.DJAmount, // 
            ExpressAmount: this.dataForm.ExpressAmount, // 
            OtherAmount: this.dataForm.OtherAmount // 
          }
          console.log(params)
          API.register.cashierSubmit(params).then(result => {
            if (result.code === '0000') {
              this.$message({
                message: `${''}`,
                type: 'success',
                duration: 1500,
                onClose: () => {
                  this.visible = false
                  this.$emit('refreshDataList')
                }
              })
            } else {
              this.$message.error(result.message)
            }
          })
          this.chenxiPrint() // 
        } else {
          this.$alert('! ', '', {
            confirmButtonText: ''
          })
        }
      })
    },
    // 
    chenxiPrint () {
      var printHTML = document.getElementById('chenxiPrint').innerHTML // 
      var page = window.open('', '_blank') // 
      page.document.write(printHTML) // 
      page.print() // 
      var userAgent = navigator.userAgent
      if ((userAgent.indexOf('compatible') > -1 && userAgent.indexOf('MSIE') > -1) || (userAgent.indexOf('Edge') > -1) || (userAgent.indexOf('Trident') > -1 && userAgent.indexOf('rv:11.0') > -1)) {
        // IE
        page.document.execCommand('print')
      } else {
        console.log('not IE')
      }
      page.close() // 
    },
    // 

    // 
    sumChinese (n) {
      if (!/^(0|[1-9]\d*)(\.\d+)?$/.test(n)) return ''
      var unit = ''
      var str = ''
      n += '00'
      var p = n.indexOf('.')
      if (p >= 0) n = n.substring(0, p) + n.substr(p + 1, 2)
      unit = unit.substr(unit.length - n.length)
      for (var i = 0; i < n.length; i++) str += ''.charAt(n.charAt(i)) + unit.charAt(i)
      return str.replace(/(|||)/g, '').replace(/()+/g, '').replace(/(||)/g, '$1').replace(/()|()/g, '$1$2').replace(/^?|/g, '').replace(/$/g, '')
    }

  }
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
.charge-info /deep/ .el-form-item {
  margin-bottom: 0;
}
.collect /deep/ {
  .el-form-item--mini .el-form-item__label {
    margin-top: -15px;
  }
  .el-form-item {
    position: relative;
    top: 6px;
  }
}

/* */
.charge-info /deep/ {
  .el-dialog__body {

  }

  .el-radio-button--mini .el-radio-button__inner {
    padding: 7px 9px;
  }
  /**/
  .v-enter {opacity: 0}
  .v-enter-active {transition: all .4s ease}
  .v-leave-active {position:absolute}
}
.ownScrollbar::-webkit-scrollbar,
.purchaseListInfo /deep/ .el-table--scrollable-y .el-table__body-wrapper::-webkit-scrollbar {
  width: 7px;
}
.ownScrollbar::-webkit-scrollbar-thumb,
.purchaseListInfo /deep/ .el-table--scrollable-y .el-table__body-wrapper::-webkit-scrollbar-thumb {
  border-radius: 3px;
  box-shadow: inset 0 0 5px rgba(0,0,0,0.1);
  background-color: #DDDEE0;
}
.ownScrollbar::-webkit-scrollbar-track,
.charge-info /deep/ .el-table--scrollable-y .el-table__body-wrapper::-webkit-scrollbar-track {
  border-radius: 0;
  box-shadow: inset 0 0 5px rgba(0,0,0,0);
  background-color: rgba(0,0,0,0);
}
</style>



