<template>
  <div>
    <el-table-column prop="" label="4" width="" header-align="center" align="center">
      <template slot-scope="scope">
        <el-button type="text" @click="addOrUpdateHandle(scope.row)" :disabled="fatherDataForm === 'see' ? true : false"></el-button>
      </template>
    </el-table-column>

    <el-table-column type="index" align="center" width="70" label=""></el-table-column>
    <el-table-column prop="CategoryName" header-align="center" align="center" label="" width="119" :show-overflow-tooltip="true"></el-table-column>
    <el-table-column prop="Code" header-align="center" align="center" label="" width="119"></el-table-column>
    <!--<el-table-column prop="BarCode" header-align="center" align="center" label="" width="119"></el-table-column>-->
    <!--<el-table-column prop="ProductName" header-align="center" align="center" label="" width="80"></el-table-column>-->
    <el-table-column prop="ShowName" header-align="center" align="center" label="" width="80"></el-table-column>

    <!--<el-table-column prop="Id" header-align="center" align="center" label="Id" width="70"></el-table-column>-->
    <!--<el-table-column prop="Quantity" header-align="center" align="center" label="" :show-overflow-tooltip="true"></el-table-column>-->
    <!--<el-table-column prop="SalePrice" header-align="center" align="center" label="" width="70"></el-table-column>-->
    <el-table-column prop="" header-align="center" :align="$store.state.common.align" label="" min-width="150">
      <template slot-scope="scope">
        <el-input-number v-model="scope.row.myNum" :step="1" @change="handleChange" :min="1" :max="10000" size="mini" :disabled="fatherDataForm === 'see' ? true : false"></el-input-number>
        <!--<div class="recipelAgeUnit">-->
        <!--<el-select v-model="categoryUnit" style="width: 45px" size="mini">-->
        <!--<el-option :label="''" :value="'g'"></el-option>-->
        <!--<el-option :label="''" :value="'dai'"></el-option>-->
        <!--</el-select>-->
        <!--</div>-->
      </template>
    </el-table-column>
    <!--<el-table-column prop="" header-align="center" :align="$store.state.common.align" label="" min-width="70">-->
    <!--<template slot-scope="scope">-->
    <!--{{Number(scope.row.SalePrice * scope.row.myNum).toFixed(2)}}-->
    <!--</template>-->
    <!--</el-table-column>-->
    <!--<el-table-column prop="SalePrice" header-align="center" align="center" label="" width="70"></el-table-column>-->
  </div>
</template>

<script>
export default {
  name: 'table-four',
  props: ['fatherDataForm'],
  data () {
    return {
      categoryUnit: ''
    }
  },
  methods: {
    addOrUpdateHandle (row) {
      // this.visible = false
      this.$emit('tableEvent', row)
    },
    handleChange () {
      // this.$nextTick(() => {})
      this.$emit('tableNumberEvent')
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .recipelAgeUnit /deep/ {
    display: inline-block;
    .el-input__inner {
      padding: 0 5px;
    }
  }
</style>



