export function changePassword (params) {
  return request({
    url: requestUrl('/YstApiAccount/ChangePassword'),
    method: 'post',
    params: requestParam(params, 'get')
  })
}

// reset
export function resetPassword (params) {
  return request({
    url: requestUrl('/YstApiAccount/ResetPassword'),
    method: 'post',
    params: requestParam(params, 'get')
  })
}



