<template>
  <el-dialog
    v-dialogDrag
    :title="''" :width="isAddActive === false ? '60%' : '85%'"
    :close-on-click-modal="false"
    :visible.sync="visible" @close="handleClose">
    <!--<el-col><div style="border-top: 1px dashed #ccc;padding-top: 10px;font-weight: 900"></div></el-col>-->
    <el-container>
      <el-aside :width="isAddActive === false ? '100%' : '60%'">
        <div class="ownScrollbar" style="min-height: 500px;max-height: 500px;overflow-y: scroll;">
          <el-row>
            <el-col style="padding: 0 50px">
              <el-steps :active="stepActive" process-status="success" finish-status="success" align-center>
                <el-step title="()" description=""></el-step>
                <el-step title="" description=""></el-step>
                <el-step title="" description=""></el-step>
              </el-steps>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8"><p><span v-if="dataList !== null">{{dataList.CreatedByName}}</span></p></el-col>
            <el-col :span="8"><p><span v-if="dataList !== null">{{dataList.SupplierName}}</span></p></el-col>
            <el-col :span="8">
              <p style="width: 190px;">
                <span v-if="dataList !== null"
                  style="display: inline-block; vertical-align: bottom; width: 134px; overflow: hidden; text-overflow:ellipsis;white-space:nowrap;">
                {{dataList.StoreName}}
              </span>
              </p>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8"><p><span v-if="dataList !== null">{{dataList.Buyer}}</span></p></el-col>
            <el-col :span="8"><p><span v-if="dataList !== null">{{dataList.Phone}}</span></p></el-col>
            <el-col :span="8"><p><span v-if="dataList !== null">{{dataList.Address}}</span></p></el-col>
          </el-row>
          <el-row>
            <el-col :span="16"><p><span v-if="dataList !== null">{{dataList.CreatedTime}}</span></p></el-col>
            <el-col :span="8"><p><span v-if="dataList !== null">{{dataList.Remark}}</span></p></el-col>
          </el-row>
          <el-row>
            <el-col><div style="padding-top: 5px;font-weight: 900;color: #1CA579"><b v-text="categoryName"></b></div></el-col>
          </el-row>

          <el-table  v-if="dataList !== null" :key="Math.random()"
                     :data="dataList.Items"
                     stripe
                     v-loading="dataListLoading"
                     row-class-name="purchaseListRow"
                     :header-cell-style="$cxObj.tableHeaderStyle40px"
                     style="width: 100%;">
            <el-table-column type="index" align="center" label="" width="50"></el-table-column>
            <el-table-column prop="ProductId" align="center" label="" width="50"></el-table-column>
            <!--<el-table-column prop="Id" align="center" label="ID" width="50"></el-table-column>-->
            <el-table-column prop="ProductName" header-align="center" align="center" label=""></el-table-column>
            <el-table-column prop="CategoryName" header-align="center" align="center" label=""></el-table-column>

            <el-table-column v-if="editType === 'A'" :key="Math.random()" prop="" header-align="center" :align="$store.state.common.align" label="" width="120">
              <template slot-scope="scope">
                <el-input-number v-model="scope.row.Quantity" :step="1" :min="1" :max="1000" size="mini" controls-position="right" style="width: 105px"></el-input-number>
              </template>
            </el-table-column>
            <el-table-column v-else :key="Math.random()" prop="Quantity" header-align="center" align="center" label=""></el-table-column>

            <el-table-column prop="CostPrice" header-align="center" align="center" label=""></el-table-column>

            <el-table-column v-if="editType === 'A'" :key="Math.random()" prop="" header-align="center" :align="$store.state.common.align" label="" width="110">
              <template slot-scope="scope">
                <el-input-number v-model="scope.row.CostPrice" :precision="2" :step="0.01" :min="0.01" :max="1000" size="mini" controls-position="right" style="width: 100px"></el-input-number>
              </template>
            </el-table-column>
            <el-table-column v-else prop="CostPrice" header-align="center" align="center" label=""></el-table-column>

            <el-table-column v-if="editType === 'A'" :key="Math.random()" prop="" header-align="center" :align="$store.state.common.align" label="" width="110">
              <template slot-scope="scope">
                <el-input-number v-model="scope.row.SalePrice" :precision="2" :step="0.01" :min="0.01" :max="1000" size="mini" controls-position="right" style="width: 100px"></el-input-number>
              </template>
            </el-table-column>
            <el-table-column v-if="dataList.Status === 4" prop="SalePrice" header-align="center" align="center" label=""></el-table-column>
            <!--<el-table-column prop="Amount" header-align="center" align="center" label=""></el-table-column>-->
            <el-table-column v-if="editType === 'B'" :key="Math.random()" prop="" header-align="center" :align="$store.state.common.align" label="" width="150">
              <template slot-scope="scope">
                <el-input v-model="scope.row.ProductBatchNo" placeholder="" size="mini"></el-input>
                <!--<el-input-number v-model="scope.row.ProductBatchNo" :step="1" :min="0" :max="10000" size="mini" controls-position="right"></el-input-number>-->
              </template>
            </el-table-column>
            <el-table-column v-if="dataList.Status === 10" prop="ProductBatchNo" header-align="center" align="center" label=""></el-table-column>
            <el-table-column v-if="editType === 'A'" prop="" label="" width="50" header-align="center" align="center">
              <template slot-scope="scope">
                <el-button type="text" @click="leftRemove(scope.row.ProductId)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-aside>

      <el-main v-if="isAddActive" style="padding: 0 0 0 5px !important; border-left: 1px solid #E6E6E6">
        <!--<el-form :model="dataForm" :rules="dataRule" ref="dataForm" label-width="0" :inline="true">-->
        <el-form class="purchaseListInfo" :model="dataForm" ref="dataForm" label-width="0" :inline="true">
          <el-form-item label="" prop="SpellName">
            <el-input v-model="dataForm.SpellName" placeholder="" size="mini" clearable>
              <el-button slot="append" icon="el-icon-search" @click="drugsSearch()"></el-button>
            </el-input>
          </el-form-item>
          <!--@selection-change="selectionChangeHandle"-->
          <!--<el-table-column type="selection" header-align="center" :align="$store.state.common.align" width="50"></el-table-column>-->
          <el-table
            :data="dataListAdd" ref="tableChild" :key="Math.random()"
            stripe
            :height="450"
            v-loading="dataListLoading"
            :header-cell-style="$cxObj.tableHeaderStyle40px"
            row-class-name="purchaseTableRowClass"
            style="width: 100%;">
            <el-table-column :align="$store.state.common.align" type="index" label="" width="50px"></el-table-column>
            <el-table-column prop="Code" header-align="center" :align="$store.state.common.align" width="90" label="" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="CategoryName" header-align="center" :align="$store.state.common.align" width="" label="" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="ShowName" header-align="center" :align="$store.state.common.align" label="" width="70" :show-overflow-tooltip="true"></el-table-column>
            <!--<el-table-column prop="Specification" header-align="center" :align="$store.state.common.align" label="" :show-overflow-tooltip="true"></el-table-column>-->
            <!--<el-table-column prop="Unit" header-align="center" :align="$store.state.common.align" label=""></el-table-column>-->
            <!--<el-table-column prop="RedLine" header-align="center" :align="$store.state.common.align" label="" :show-overflow-tooltip="true"></el-table-column>-->
            <el-table-column prop="Quantity" header-align="center" :align="$store.state.common.align" label="" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="" label="" width="50" header-align="center" align="center">
              <template slot-scope="scope">
                <el-button type="text" @click="rightAdd(scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-form>
      </el-main>
    </el-container>
    <span v-if="editType !== ''" slot="footer" class="dialog-footer">
      <el-button @click="visible = false"></el-button>
      <el-button v-if="editType === 'A'" type="primary" @click="dataFormAdd()"></el-button>
      <el-button v-if="editType === 'A'" type="primary" @click="dataFormSubmitA()"></el-button>
      <el-button v-else-if="editType === 'B'" type="primary" @click="dataFormSubmitB()"></el-button>
    </span>
  </el-dialog>
</template>
<script type="text/ecmascript-6">
import API from '@/api'
import {Currency, Letter, NumberInt, NumberFloat} from '../../utils/validate'
// import {treeDataTranslate} from '@/utils'
export default {
  components: {
  },
  data () {
    return {
      stepActive: -1,
      visible: false,
      dataListLoading: false, // 
      Id: '',
      dataForm: {
        SpellName: ''
      },
      dataList: null,
      editType: '', // AB
      categoryName: '',
      categoryId: '',

      isAddActive: false,
      dataListAdd: []
    }
  },
  methods: {
    comFunction () {
      //   
      API.drugs.getDrugsList({
        Name: '',
        PageIndex: 1,
        PageSize: 10000,
        IsPaging: 'false',
        SpellName: this.dataForm.SpellName,
        CategoryId: this.categoryId,
        StoreId: this.dataList.StoreId,
        CodeOrBarCode: ''
      }).then(result => {
        if (result.code === '0000') {
          this.dataListAdd = result.data
          this.dataList.Items.forEach(item0 => {
            this.dataListAdd = this.dataListAdd.filter(item => {
              return item0.ProductCode !== item.Code
            })
          })
        } else {
          this.$message({ message: '', type: 'warning', duration: 3000 })
        }
      })
    },
    //   
    dataFormAdd () {
      this.isAddActive = !this.isAddActive // '[]'
      if (this.isAddActive === false) {
        return false
      }
      this.comFunction()
    },
    leftRemove (ProductId) {
      this.dataList.Items = this.dataList.Items.filter(item => {
        return item.ProductId !== ProductId
      })
      if (this.isAddActive === true) {
        this.comFunction()
      }
    },
    rightAdd (row) {
      // el-table
      API.drugs.getDrugsList({name: '', PageIndex: 1, PageSize: 10000, IsPaging: 'false', SpellName: this.dataForm.SpellName, CategoryId: this.categoryId}).then(result => {
        if (result.code === '0000') {
          result.data.forEach(item => {
            if (item.Code === row.Code) {
              // this.dataList.Items.push(item) // tablerow
              this.dataList.Items.push({
                Id: '', // id CategoryName
                ProductId: item.Id, // need idid
                SapProductCode: item.Code, // need
                ProductCode: item.Code, // need
                ProductName: item.ShowName, // need
                Specification: null,
                Unit: 'g',
                CostPrice: item.SalePrice, // need
                SalePrice: 0,
                Preferential: 0,
                CategoryId: '',
                CategoryName: item.CategoryName, // need
                Quantity: item.Quantity, // need
                Amount: 0.33,
                ActualShipAmount: 0,
                ActualAmount: 0,
                ActualShipQuantity: 0,
                ActualQuantity: 0,
                BgColor: 'bg-danger',
                SupplierId: 0, // ???
                SupplierCode: null, // ???
                SupplierName: null,
                InventoryQuantity: 0,
                BatchNo: 0,
                ProductBatchNo: null,
                CargoFee: 0,
                Pictures: [
                  '/Content/AdminLTE/img/default-50x50.gif',
                  '/Content/AdminLTE/img/default-50x50.gif',
                  '/Content/AdminLTE/img/default-50x50.gif'
                ],
                StoreId: '', // ???,
                SapStoreCode: null,
                ShippedQuantityByInventory: 0,
                ShippedQuantityByFactory: 0,
                ActualOrderQuantity: 0
              })
              console.log(this.dataList.Items)
              return false
            }
          })
          this.$nextTick(() => {
            console.log('')
            if (this.isAddActive === true) {
              this.comFunction()
            }
          })
        }
      })
    },
    // info
    init (id, type) {
      this.visible = true
      this.dataListLoading = true
      this.editType = type !== undefined ? type : '' // A B input
      if (id !== undefined) {
        API.purchase.getPurchaseInfo({id: id}).then(result => {
          if (result.code === '0000') {
            this.dataList = result.data
            this.categoryId = this.dataList.Items[0].CategoryId
            this.categoryName = this.dataList.Items[0].CategoryName // 
            switch (result.data.Status) {
              case -1:
                this.stepActive = -1
                break
              case 1:
                this.stepActive = 0
                break
              case 4:
                this.stepActive = 1
                break
              case 10:
                this.stepActive = 2
                break
            }
            this.dataListLoading = false
          }
        })
      }
    },
    handleClose () {
      this.editType = ''
      this.isAddActive = false
    },
    dataFormSubmitA () { //  
      var params = {
        Address: this.dataList.Address,
        Buyer: this.dataList.Buyer,
        Phone: this.dataList.Phone,
        OrderType: this.dataList.OrderType,
        Remark: this.dataList.Remark,
        StoreId: this.dataList.StoreId,
        StoreCode: this.dataList.StoreCode,
        Id: this.dataList.Id,
        SupplierId: this.dataList.SupplierId,
        SupplierCode: this.dataList.SupplierCode,
        Items: JSON.stringify(this.dataList.Items.map(item => {
          return {
            ProductId: item.ProductId,
            CostPrice: item.CostPrice,
            Quantity: item.Quantity,
            SapProductCode: item.SapProductCode,
            SupplierId: this.dataList.SupplierId,
            SupplierCode: this.dataList.SupplierCode // 9item.SupplierId
          }
          // [{\"ProductId\":23,\"CostPrice\":0,\"Quantity\":3,\"SapProductCode\":\"10000109\",\"SupplierId\":1,\"SupplierCode\":\"6006\"}
        }))
      }
      console.log(params)
      API.purchase.editNumberAndPrice(params).then(result => {
        if (result.code === '0000') {
          this.$message({
            type: 'success',
            message: `${result.message}`,
            duration: 1000
          })
          this.$emit('refreshDataList')
          this.visible = false
        } else {
          this.$message({
            type: 'error',
            message: `${result.message}`,
            duration: 1000
          })
        }
      })
    },
    dataFormSubmitB () { //  
      if (this.dataList.Items.some(item => item.ProductBatchNo === 0 || item.ProductBatchNo === '' || item.ProductBatchNo === null)) {
        this.$alert('!', '', {
          confirmButtonText: ''
        })
        return false
      }
      var params = {
        Code: this.dataList.Code,
        Id: this.dataList.Id,
        Remark: this.dataList.Remark,
        Items: JSON.stringify(this.dataList.Items.map(item => {
          return {
            Id: item.Id, // idAID
            ActualQuantity: item.Quantity,
            productBatchNo: item.ProductBatchNo
          }
          // "[{\"Id\":96,\"ActualQuantity\":1,\"productBatchNo\":123123}, ]       }))
        }))
      }
      console.log(params)
      API.purchase.editBatchNo(params).then(result => {
        if (result.code === '0000') {
          this.$message({
            type: 'success',
            message: `${result.message}`,
            duration: 1000
          })
          this.$emit('refreshDataList')
          this.visible = false
        } else {
          this.$message({
            type: 'error',
            message: `${result.message}`,
            duration: 1000
          })
        }
      })
    }
  }
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
.ownScrollbar::-webkit-scrollbar,
.purchaseListInfo /deep/ .el-table--scrollable-y .el-table__body-wrapper::-webkit-scrollbar {
  width: 7px;
}
.ownScrollbar::-webkit-scrollbar-thumb,
.purchaseListInfo /deep/ .el-table--scrollable-y .el-table__body-wrapper::-webkit-scrollbar-thumb {
  border-radius: 3px;
  box-shadow: inset 0 0 5px rgba(0,0,0,0.1);
  background-color: #DDDEE0;
}
.ownScrollbar::-webkit-scrollbar-track,
.purchaseListInfo /deep/ .el-table--scrollable-y .el-table__body-wrapper::-webkit-scrollbar-track {
  border-radius: 0;
  box-shadow: inset 0 0 5px rgba(0,0,0,0);
  background-color: rgba(0,0,0,0);
}
</style>



