<template>
  <el-dialog
    v-dialogDrag
    :title="$store.getters.getRegisterStep === 1 ? '' : $store.getters.getRegisterStep === 2 ? '' : ''" :width="'678px'"
    :close-on-click-modal="false"
    :visible.sync="outerVisible" @close="handleClose" class="registerIndex">
    <!--vuex 123-->
    <!--one div-->
    <transition mode="out-in">
    <div v-if="show">
      <div class="ownScrollbar" style="min-height: 420px;overflow-y: scroll;">
        <el-form ref="dataForm" :rules="dataRule" :model="dataForm" label-width="70px" size="mini" :inline="true">
          <el-row>
            <el-col><div style="padding-top: 5px; font-size: 16px; font-weight: 900;color: #1CA579"></div></el-col>
          </el-row>
          <div style="padding-left: 32px">
            <el-row>
              <el-col :span="12">
                <el-form-item label="" prop="UserName">
                  <el-input v-model="dataForm.UserName" placeholder="" style="width: 80px" disabled></el-input>
                  <span class="iconfont icon-renwu-zengjia" style="display: inline-block;width: 40px;height: 30px;
                              font-size: 26px;font-weight: 900;color: #1EA57B; margin-left: 5px; vertical-align: top"
                        @click="show = !show; openPatientList(); $store.commit('setRegisterStep', 2)" ></span>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="">
                  <el-input v-model="dataForm.Sex" placeholder="" style="width: 80px" disabled></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="12">
                <el-form-item label="">
                  <el-input v-model="dataForm.BirthDate" placeholder="" style="width: 80px" disabled></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="">
                  <el-input v-model="dataForm.MobilePhone" placeholder="" style="width: 140px" disabled></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12"><p><span v-text="dataForm.Code"></span></p></el-col>
              <el-col :span="12"><p><span v-text="dataForm.MobilePhone"></span></p></el-col>
            </el-row>
            <el-row>
              <el-col :span="24">
                <el-form-item label="">
                  <el-input v-model="dataForm.Address" placeholder="" style="width: 465px" disabled></el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </div>
          <!---->
          <el-row>
            <el-col><div style="padding-top: 5px; font-size: 16px; font-weight: 900;color: #1CA579"></div></el-col>
          </el-row>
          <div style="padding-left: 48px"><p><span v-text="doctorName"></span></p></div>

          <el-row>
            <el-col><div style="padding-top: 5px;font-size: 16px;font-weight: 900;color: #1CA579"></div></el-col>
          </el-row>
          <div style="padding-left: 32px">
            <!--<el-row>-->
              <!--<el-col :span="12"><p><span v-text="'201904190001'"></span></p></el-col>-->
              <!--<el-col :span="12"><p><span v-text="'2019-04-19 12:00:00'"></span></p></el-col>-->
            <!--</el-row>-->
            <!---->
            <el-row>
              <el-col :span="24">
                <el-form-item label=""><!---->
                  <el-radio-group v-model="dataForm.DiagnosisType">
                    <el-radio-button label="1"></el-radio-button>
                    <el-radio-button label="2"></el-radio-button>
                  </el-radio-group>
                </el-form-item>
              </el-col>
            </el-row>
            <!---->
            <el-row>
              <el-col :span="12">
                <el-form-item label=""><!---->
                  <el-select v-model="dataForm.chargeType" style="width: 100px"
                             @change="chargeTypeChange" placeholder="">
                    <el-option v-for="item in optionsChargeType"
                               :key="item.value" :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>

            <!---->
            <el-row>
              <el-col :span="12">
                <el-form-item label=""><!---->
                  <el-select v-model="dataForm.PaymentWay" style="width: 100px" placeholder="">
                    <el-option v-for="item in optionsPaymentType" :key="item.value"
                               :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="" prop="RegisterAmount">
                  <el-input v-model="dataForm.RegisterAmount" placeholder=""
                            style="width: 85px" clearable :disabled="RegisterBool"></el-input> 
                </el-form-item>
              </el-col>
            </el-row>
          </div>
        </el-form>
      </div>

      <el-dialog
        width="500px"
        title=""
        :visible.sync="innerVisible"
        append-to-body>
        <!--12.00 * 93.10 chrome-->
        <div id="chenxiPrint">
          <table width="100%" style="font-size: 12px;padding-right: 55px">
            <tbody>
            <tr>
              <td colspan="3" align="center" height="24" style="margin-bottom: 20px;font-weight: 600"><h3></h3></td>
            </tr>
            <tr>
              <td colspan="3" height="24">{{registerAllData.UserName}} {{registerAllData.SexName ? registerAllData.SexName : '__'}} {{registerAllData.BirthDate}}</td>
            </tr>
            <tr>
              <td colspan="2" height="24">{{registerAllData.Code}}</td>
              <td colspan="1" align="right" v-if="registerAllData.CreatedOnTime">{{registerAllData.CreatedOnTime | myDateFilter('yyyy/MM/dd hh:mm:ss')}}</td>
            </tr>
            <tr>
              <td colspan="2" height="24">{{registerAllData.DoctorName}}</td>
              <td colspan="1" align="right" width="240">{{registerAllData.Code}}</td>
            </tr>

            <tr valign="bottom" style="font-size: 12px">
              <td height="30"></td>
              <td colspan="2"><p>
                <span style="display: inline-block;width: 200px;text-align: right"></span></p></td>
            </tr>
            <tr>
              <td></td>
              <td colspan="2"><p>{{registerAllData.RegisterAmount}}
                <span style="display: inline-block;width: 200px;text-align: right"></span></p></td>
            </tr>
            <!--<tr>-->
            <!--<td></td>-->
            <!--<td colspan="2"><p>{{registerAllData.}}-->
            <!--<span style="display: inline-block;width: 200px;text-align: right"></span></p></td>-->
            <!--</tr>-->
            <tr valign="bottom">
              <td height="50"></td>
              <td colspan="2"><p>
                <span style="display: inline-block;width: 210px;text-align: right">{{registerAllData.PaymentWayName}}</span></p></td>
            </tr>

            <tr>
              <td colspan="1">{{registerAllData.RegisterAmount}}</td>
              <td colspan="2"><p>{{sumChinese(registerAllData.RegisterAmount)}}</p></td>
            </tr>
            <tr>
              <td colspan="3">15! </td>
            </tr>
            <tr>
              <td colspan="3">xx</td>
            </tr>
            </tbody>
          </table>
        </div>
        <div style="text-align: right"><el-button type="primary" @click="chenxiPrint()"></el-button></div>
      </el-dialog>

      <div style="text-align: right; margin-top: 30px">
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="dataFormSubmit()"></el-button>
          <el-button @click="outerVisible = false"></el-button>
          <!--<el-button type="primary" @click="innerVisible = true"> Dialog</el-button>-->
        </span>
      </div>

    </div>
    </transition>

    <transition mode="out-in">
      <!--two div-->
      <div v-if="!show">
        <first-patient-list v-if="addOrUpdateVisible" ref="patientList" @childEven="patientListFun"></first-patient-list>
      </div>
    </transition>
  </el-dialog>
</template>
<script type="text/ecmascript-6">
import API from '@/api'
import {Currency, Letter, NumberInt, NumberFloat, calcAge} from '../../utils/validate'
import '../common/icon/iconfont.css'
// import {treeDataTranslate} from '@/utils'
import FirstPatientList from './first-patient-list'
export default {
  components: { FirstPatientList },
  watch: {
    'dataForm.RegisterAmount': function (newval, oldval) {
      if (String(newval).replace(/\s/g, '').length !== String(newval).length) {
        this.$alert(' ! ', '', {
          confirmButtonText: '',
          callback: () => {
            this.dataForm.RegisterAmount = ''
          }
        })
      }
      if (Number(newval) === 0) { return false }
      if (!this.regMoney.test(newval)) {
        this.$alert('! ', '', {
          confirmButtonText: '',
          callback: () => {
            this.dataForm.RegisterAmount = ''
          }
        })
      }
    }
  },
  data () {
    return {
      outerVisible: false,
      innerVisible: false,
      regMoney: /^\d+\.?\d{0,2}$/,
      show: true,
      dataListLoading: false, // 

      doctorId: '',
      doctorName: '',
      dataForm: {
        UserName: '',
        Sex: '',
        BirthDate: '',
        MobilePhone: '',
        Address: '',
        UserId: '',
        Code: '',

        PaymentWay: 1, // 
        DiagnosisType: '1', // 
        chargeType: '', // 
        RegisterAmount: '', //  ''
        ConsultationAmount: 0, // 
        reality: 0, // 
        give: 0 // 
      },
      RegisterBool: false, // 

      dataList: null,
      addOrUpdateVisible: false,
      dataRule: {
        UserName: Currency(''),
        RegisterAmount: Currency(''),
        ConsultationAmount: Currency(''),
        reality: Currency(''),
        give: Currency('')
      },
      optionsChargeType: [
        { // 
          value: '0',
          label: '0'
        }, {
          value: '',
          label: ''
        }
      ],
      optionsPaymentType: [
        { // 
          label: '',
          value: 1
        }, {
          label: '',
          value: 2
        }, {
          label: '',
          value: 3
        }, {
          label: '',
          value: 4
        }, {
          label: '',
          value: 5
        }, {
          label: '',
          value: 6
        }, {
          label: '',
          value: 7
        }, {
          label: '',
          value: 8
        }
      ],
      registerAllData: {}
    }
  },
  created () {
    this.$store.commit('setRegisterStep', 1)
  },
  methods: {
    patientListFun (row) { // 
      this.show = !this.show
      this.dataForm.UserName = row.UserName
      this.dataForm.Sex = row.Sex === 1 ? '' : ''
      this.dataForm.BirthDate = row.BirthDate
      this.dataForm.MobilePhone = row.MobilePhone
      this.dataForm.Address = row.Address
      this.dataForm.UserId = row.Id
      this.dataForm.Code = row.Code
    },
    // info
    init (row) {
      this.outerVisible = true
      this.dataListLoading = true
      if (row !== undefined) {
        this.doctorId = row.Id // id
        this.doctorName = row.NickName
        this.dataForm.RegisterAmount = row.RegisterAmount
      }
      this.dataListLoading = false
    },
    chargeTypeChange (val) {
      switch (val) {
        case '0':
          this.dataForm.RegisterAmount = 0
          this.dataForm.ConsultationAmount = 0
          this.RegisterBool = true
          break
        case '':
          this.dataForm.RegisterAmount = ''
          this.dataForm.ConsultationAmount = 0
          this.RegisterBool = false
          break
      }
    },
    handleClose () {
      this.show = true
      this.$store.commit('setRegisterStep', 1)
      this.doctorId = ''
      this.doctorName = ''
      this.dataForm = {
        UserName: '',
        Sex: '',
        BirthDate: '',
        MobilePhone: '',
        Address: '',
        UserId: '',
        Code: '',

        PaymentWay: 1, // 
        DiagnosisType: '1', // 
        chargeType: '', // 
        RegisterAmount: '', // 
        ConsultationAmount: 0 // 
      }
      this.RegisterBool = false // 
      this.$refs.dataForm.clearValidate()
    },

    // 
    dataFormSubmit () {
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          var params = {
            StoreId: this.$store.getters.getAccountCurrentHandleStore,
            AccountId: this.doctorId, // id
            UserId: this.dataForm.UserId, // id
            OrderType: 1, // 1 2
            DiagnosisType: this.dataForm.DiagnosisType, //  

            RegisterAmount: this.dataForm.RegisterAmount, // 
            ConsultationAmount: this.dataForm.ConsultationAmount, // 
            RegisterStatus: 2, // 1 2 99.99%2

            PaymentWay: this.dataForm.PaymentWay,
            Remark: this.dataForm.PaymentWay
          }
          console.log(params)
          API.register.registerSubmit(params).then(result => {
            if (result.code === '0000') {
              this.$message({
                message: `${''}`,
                type: 'success',
                duration: 3000
              })
              // 
              API.register.getRegisterInfo({id: result.id}).then(result => {
                if (result.code === '0000') {
                  console.log(result.data)
                  result.data.BirthDate = calcAge(result.data.BirthDate)
                  this.registerAllData = result.data
                  this.$nextTick(() => { this.innerVisible = true })
                }
                this.outerVisible = false
              })
            } else {
              this.$message.error(result.message)
            }
          })
        } else {
          this.$alert('! ', '', {
            confirmButtonText: ''
          })
        }
      })
    },

    // 
    chenxiPrint () {
      // console.log('', this.$refs.chenxiPrint)
      var printHTML = document.getElementById('chenxiPrint').innerHTML // 
      var page = window.open('', '_blank') // 
      page.document.write(printHTML) // 
      page.print() // 
      var userAgent = navigator.userAgent
      if ((userAgent.indexOf('compatible') > -1 && userAgent.indexOf('MSIE') > -1) || (userAgent.indexOf('Edge') > -1) || (userAgent.indexOf('Trident') > -1 && userAgent.indexOf('rv:11.0') > -1)) {
        page.document.execCommand('print')
      } else {
        console.log('not IE')
      }
      page.close() // 
      this.innerVisible = false
    },
    // 

    // 
    sumChinese (n) {
      if (!/^(0|[1-9]\d*)(\.\d+)?$/.test(n)) return ''
      var unit = ''
      var str = ''
      n += '00'
      var p = n.indexOf('.')
      if (p >= 0) n = n.substring(0, p) + n.substr(p + 1, 2)
      unit = unit.substr(unit.length - n.length)
      for (var i = 0; i < n.length; i++) str += ''.charAt(n.charAt(i)) + unit.charAt(i)
      return str.replace(/(|||)/g, '').replace(/()+/g, '').replace(/(||)/g, '$1').replace(/()|()/g, '$1$2').replace(/^?|/g, '').replace(/$/g, '')
    },

    openPatientList () {
      this.addOrUpdateVisible = true
      this.$nextTick(() => {
        this.$refs.patientList.init()
      })
    }
  }
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
.registerIndex /deep/ .el-form-item {
  margin-bottom: 7px;
}

/* */
.registerIndex /deep/ {
  .el-radio-button--mini .el-radio-button__inner {
    padding: 7px 9px;
  }
  /**/
  .v-enter {opacity: 0}
  .v-enter-active {transition: all .4s ease}
  .v-leave-active {position:absolute}
}
.ownScrollbar::-webkit-scrollbar,
.purchaseListInfo /deep/ .el-table--scrollable-y .el-table__body-wrapper::-webkit-scrollbar {
  width: 7px;
}
.ownScrollbar::-webkit-scrollbar-thumb,
.purchaseListInfo /deep/ .el-table--scrollable-y .el-table__body-wrapper::-webkit-scrollbar-thumb {
  border-radius: 3px;
  box-shadow: inset 0 0 5px rgba(0,0,0,0.1);
  background-color: #DDDEE0;
}
.ownScrollbar::-webkit-scrollbar-track,
.purchaseListInfo /deep/ .el-table--scrollable-y .el-table__body-wrapper::-webkit-scrollbar-track {
  border-radius: 0;
  box-shadow: inset 0 0 5px rgba(0,0,0,0);
  background-color: rgba(0,0,0,0);
}
</style>



