<template>
  <el-dialog
    v-dialogDrag
    :title="!dataForm.id ? '' : ''" width="600px"
    :close-on-click-modal="false"
    :visible.sync="visible" @close="handleClose">
    <el-form :model="dataForm" :rules="dataRule" ref="dataForm" label-width="80px" size="small" :inline="true">
      <el-form-item label="" prop="Name">
        <el-input v-model="dataForm.Name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="dataForm.CategoryId" placeholder="" @change="categoryChange" style="width: 184px">
          <el-option v-if="item.pId !== null" v-for="item in drugsCategoryList" :key="item.id" :label="item.text" :value="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="ShowName">
          <el-input v-model="dataForm.ShowName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="SpellName">
          <el-input v-model="dataForm.SpellName" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="" prop="Unit">
        <el-select v-model="dataForm.Unit" placeholder="" style="width: 184px">
          <el-option label="" value="g"></el-option>
          <el-option label="" value="ML"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="Specification">
        <el-input v-model="dataForm.Specification" placeholder="x*x/g"></el-input>
      </el-form-item>
      <!--<el-form-item label="" prop="SalePrice">-->
        <!--<el-input v-model="dataForm.SalePrice" placeholder="2"></el-input>-->
      <!--</el-form-item>-->
      <el-form-item label="" prop="RedLine">
        <el-input v-model="dataForm.RedLine" placeholder=""></el-input>
      </el-form-item>

      <!---->
      <el-row>
        <el-col><div style="border-top: 1px dashed #ccc;padding-top: 10px;font-weight: 900"></div></el-col>
      </el-row>
      <el-row>
        <el-col :span="6" :offset="6">
          <el-form-item label="">
            <el-input v-model="dataForm.Keywords0" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="Keywords1">
            <el-input v-model="dataForm.Keywords1" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6" :offset="6">
          <el-form-item label="">
            <el-input v-model="dataForm.Keywords2" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="Keywords3">
            <el-input v-model="dataForm.Keywords3" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6" :offset="6">
          <el-form-item label="">
            <el-input v-model="dataForm.Keywords4" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="Keywords5">
            <el-input v-model="dataForm.Keywords5" placeholder=""></el-input>
          </el-form-item>
        </el-col>
      </el-row>

    </el-form>

    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false"></el-button>
      <el-button type="primary" @click="dataFormSubmit()"></el-button>
    </span>
  </el-dialog>
</template>
<script type="text/ecmascript-6">
import API from '@/api'
import {Currency, Letter, NumberInt, NumberFloat, pinyinChenXi} from '../../utils/validate'
// import {treeDataTranslate} from '@/utils'
export default {
  components: {},
  watch: {
    'dataForm.ShowName': function (val, oldval) {
      var pinyin = pinyinChenXi() // 
      this.dataForm.SpellName = pinyin.getCamelChars(val)
    }
  },
  data () {
    return {
      visible: false,
      drugsCategoryList: [], // 

      Id: '',
      dataForm: {
        Name: '', // 
        CategoryId: '1001',
        CategoryName: '',

        ShowName: '', // 
        SpellName: '',
        Unit: 'g',
        Specification: '',
        SalePrice: '',
        RedLine: 0,

        Keywords0: '',
        Keywords1: '',
        Keywords2: '',
        Keywords3: '',
        Keywords4: '',
        Keywords5: ''
      },

      dataRule: {
        Name: Currency(''),
        ShowName: Currency(''),
        // SpellName: Letter(1),
        Unit: Currency(''),
        // SalePrice: NumberFloat(),
        RedLine: NumberInt(),
        Keywords1: Letter(),
        Keywords3: Letter(),
        Keywords5: Letter()
      }
    }
  },
  methods: {
    // info
    init (id) {
      this.visible = true
      API.drugs.getDrugsCategory().then(result => {
        if (result.code === '0000') {
          this.drugsCategoryList = result.data
          this.$nextTick(() => {
            if (id) {
              API.drugs.getEdit({id: id}).then(result => {
                if (result.code === '0000') {
                  this.dataForm = {
                    Name: result.data.Name, // 
                    CategoryId: result.data.CategoryId,
                    CategoryName: result.data.CategoryName,

                    ShowName: result.data.ShowName, // 
                    SpellName: result.data.SpellName,
                    Unit: result.data.Unit,
                    Specification: result.data.Specification,
                    SalePrice: '',
                    RedLine: result.data.RedLine,
                    Keywords0: result.data.Keywords.split(',')[0],
                    Keywords1: result.data.Keywords.split(',')[1],
                    Keywords2: result.data.Keywords.split(',')[2],
                    Keywords3: result.data.Keywords.split(',')[3],
                    Keywords4: result.data.Keywords.split(',')[4],
                    Keywords5: result.data.Keywords.split(',')[5],
                  }
                  this.Id = result.data.Id
                }
                console.log(this.dataForm)
              })
            }
          })
        }
      })
    },

    handleClose () {
      this.$refs['dataForm'].resetFields()
      this.Id = ''
      this.dataForm = {
        Name: '', // 
        CategoryId: '1001',
        CategoryName: '',
        ShowName: '', // 
        SpellName: '',
        Unit: 'g',
        Specification: '',
        SalePrice: '',
        RedLine: 0,
        Keywords0: '',
        Keywords1: '',
        Keywords2: '',
        Keywords3: '',
        Keywords4: '',
        Keywords5: ''
      }
    },
    categoryChange (id) {
      this.drugsCategoryList.forEach(item => {
        if (item.id === id) {
          this.dataForm.CategoryName = item.text
          return false
        }
      })
    },
    // 
    dataFormSubmit () {
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          var params = {
            Id: this.Id,
            Name: this.dataForm.Name, // 
            CategoryId: this.dataForm.CategoryId,
            CategoryName: this.dataForm.CategoryName,

            ShowName: this.dataForm.ShowName,
            SpellName: this.dataForm.SpellName,
            Unit: this.dataForm.Unit,
            Specification: this.dataForm.Specification,
            SalePrice: '',
            RedLine: this.dataForm.RedLine,
            Keywords: [
              this.dataForm.Keywords0, this.dataForm.Keywords1,
              this.dataForm.Keywords2, this.dataForm.Keywords3,
              this.dataForm.Keywords4, this.dataForm.Keywords5
            ].join()
          }
          console.log(params)
          var tick = !this.Id ? API.drugs.createDrugs(params) : API.drugs.submitEdit(params)
          tick.then((data) => {
            if (data.code === '0000') {
              this.$message({
                message: `${this.id ? '' : ''}`,
                type: 'success',
                duration: 1500,
                onClose: () => {
                  this.visible = false
                  this.$emit('refreshDataList')
                }
              })
            } else {
              this.$message.error(data.message)
            }
          })
        }
      })
    }

  }
}
</script>



