<template>
  <el-dialog
    v-dialogDrag
    :title="!dataForm.id ? '' : ''" width="900px"
    :close-on-click-modal="false"
    :visible.sync="visible" @close="handleClose">
    <el-row v-loading="dataListLoading">
      <el-col :span="15">
        <el-form :model="dataForm" :rules="dataRule" ref="dataForm" label-width="80px" size="small" :inline="true">
          <el-form-item label="" prop="UserName">
            <el-input v-model="dataForm.UserName" placeholder="" disabled style="width: 160px"></el-input>
          </el-form-item>
          <el-form-item label="" prop="Sex">
            <el-radio-group v-model="dataForm.Sex" disabled style="width: 144px">
              <el-radio label="1"></el-radio>
              <el-radio label="2"></el-radio>
            </el-radio-group>
          </el-form-item>

          <el-form-item label="" prop="BirthDateAge">
            <el-input v-model="dataForm.BirthDateAge" @blur="stampCalc(dataForm.BirthDateAge, dataForm.BirthDateUnit)" disabled placeholder="" style="width: 91px"></el-input>
            <el-select v-model="dataForm.BirthDateUnit" @change="stampCalc(dataForm.BirthDateAge, dataForm.BirthDateUnit)" disabled style="width: 66px;margin-right: 10px">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="BirthDate">
            <el-date-picker type="date" value-format="yyyy-MM-dd" v-model="dataForm.BirthDate"
                            placeholder="" disabled style="width: 149px">
            </el-date-picker>
          </el-form-item>

          <el-form-item label="" prop="MobilePhone">
            <el-input v-model="dataForm.MobilePhone" placeholder="" disabled style="width: 160px"></el-input>
          </el-form-item>
          <br>
          <el-form-item label="" prop="Source">
            <el-select v-model="dataForm.Source" disabled style="width: 160px">
              <el-option v-for="item in memberOrigin" :key="item.lab"
                         :label="item.lab" :value="item.val">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" prop="AllergyHistory">
            <el-input v-model="dataForm.AllergyHistory" placeholder="" disabled style="width: 413px"></el-input>
          </el-form-item>
          <el-form-item label="" prop="Address">
            <el-input v-model="dataForm.Address" placeholder="" disabled style="width: 413px"></el-input>
          </el-form-item>

          <!---->
          <el-form-item label="" prop="Points">
            <el-input v-model="dataForm.Points" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="EditPoints">
            <el-input-number v-model="dataForm.EditPoints"
                             :min="-dataForm.Points" :max="10000" :step="100"></el-input-number>
          </el-form-item>
          <el-form-item label="" prop="Remark">
            <el-input
              type="textarea" style="width: 410px"
              placeholder=""
              v-model="dataForm.Remark"
              maxlength="150"
              :rows="3"
              show-word-limit>
            </el-input>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :span="9">
        <!---->
        <h3><span style="color: #e4393c"> {{dataForm.Points}}</span></h3>
        <div class='time-line' v-if="testList.length > 0">
          <div v-for='item in testList' :key="item.time" class='time-line-items'>
            <i></i><strong></strong>
            <!--<span>{{item.time | myDateFilter('yyyy/MM/dd hh:mm:ss')}}</span>-->
            <span>{{item.time | myDateFilter('yyyy/MM/dd hh')}} </span>
            <div>
              <p><b></b>{{item.people}}</p>
              {{item.text}}
            </div>
          </div>
        </div>
        <div class='time-line' v-else style="line-height: 450px;height: 450px;text-align: center;color: #e4393c"> </div>
      </el-col>
    </el-row>

    <span slot="footer" class="dialog-footer">
      <el-button type="primary" @click="dataFormSubmit()"></el-button>
      <el-button @click="visible = false"></el-button>
    </span>
  </el-dialog>
</template>
<script type="text/ecmascript-6">
import API from '@/api'
import {Currency, Phone, NumberInt} from '../../utils/validate'
import {formatDate, calcAge, calcTimeStamp} from '@/utils/validate'

export default {
  data () {
    return {
      testList: [],
      visible: false,
      dataListLoading: false, // 

      activities: [{
        content: '',
        timestamp: '2018-04-15'
      }, {
        content: '',
        timestamp: '2018-04-13'
      }, {
        content: '',
        timestamp: '2018-04-11'
      }],
      drugsCategoryList: [], // 
      memberId: ' ', //  IDaddedit
      dataForm: {
        UserName: '',
        Sex: '1',
        BirthDate: '',
        BirthDateAge: '',
        BirthDateUnit: '1', // str
        MobilePhone: '',
        Source: '20', // 
        AllergyHistory: '', // 
        Address: '',

        Points: '',
        EditPoints: '',
        Remark: ''
      },
      memberOrigin: [
        {lab: '', val: '1'},
        {lab: '', val: '2'},
        {lab: '', val: '3'},
        {lab: '', val: '4'},
        {lab: '', val: '5'},
        {lab: '', val: '6'},
        {lab: '', val: '20'}
      ],
      dataRule: {
        UserName: Currency(''),
        BirthDateAge: NumberInt(),
        BirthDate: Currency(''),
        Source: Currency(''),
        MobilePhone: Phone(1),
        Points: Currency(''),
        EditPoints: Currency(''),
        Remark: Currency('')
      }
    }
  },
  watch: {
    'dataForm.BirthDate': function (val, oldval) {
      if (val === null) {
        this.dataForm.BirthDateUnit = '1'
        this.dataForm.BirthDateAge = ''
      } else {
        var allAge = calcAge(val) // !!!!!!18  10 1
        if (allAge.substr(allAge.length - 1) === '') {
          this.dataForm.BirthDateUnit = '0'
        } else if (allAge.substr(allAge.length - 1) === '') {
          this.dataForm.BirthDateUnit = '1'
        }
        this.dataForm.BirthDateAge = allAge.substring(0, allAge.length - 1) // !!!!!!parseInt
      }
    }
  },
  methods: {
    // age
    stampCalc (age, unit) {
      if (unit === '1' && age > 120) { // 120
        this.dataForm.BirthDateUnit = '1'
        this.dataForm.BirthDateAge = ''
        return false
      } else if (unit === '0' && age > 12) { // 12
        this.dataForm.BirthDateUnit = '1'
        this.dataForm.BirthDateAge = ''
        return false
      }

      if (age === '' || age === '0' || !/^\d+$/.test(age)) {
        this.dataForm.BirthDateUnit = '1'
        this.dataForm.BirthDateAge = ''
        return false
      }
      this.dataForm.BirthDate = calcTimeStamp(age, unit)
    },
    // info
    init (id) {
      this.visible = true
      this.dataListLoading = true
      this.memberId = id
      if (id !== undefined) {
        this.$nextTick(() => {
          function memberInfo () { return API.member.editMemberGet({id: id}) }
          function seeIntegral () { return API.member.seeIntegral({id: id}) }
          this.$ios.all([memberInfo(), seeIntegral()]).then(this.$ios.spread((result, response) => {
            if (result.code === '0000' && response.code === '0000') {
              this.dataForm.UserName = result.data.UserName
              this.dataForm.Sex = String(result.data.Sex)

              this.dataForm.BirthDate = formatDate(new Date(result.data.BirthDate
                .substring(6, result.data.BirthDate.length - 2) * 1), 'yyyy-MM-dd')
              var allAge = calcAge(this.dataForm.BirthDate) // !!!!!!18  10 1
              if (allAge.substr(allAge.length - 1) === '') {
                this.dataForm.BirthDateUnit = '0'
              } else if (allAge.substr(allAge.length - 1) === '') {
                this.dataForm.BirthDateUnit = '1'
              }
              this.dataForm.BirthDateAge = allAge.substring(0, allAge.length - 1) // !!!!!! parseInt

              this.dataForm.MobilePhone = result.data.MobilePhone
              this.dataForm.AllergyHistory = result.data.AllergyHistory
              this.dataForm.Address = result.data.Address
              this.dataForm.Source = String(result.data.Source)
              this.dataForm.Points = result.data.Points
              this.memberId = result.data.Id


              // 
              console.log(result.data, response.data)
              this.testList = response.data.map(item => {
                return {
                  people: item.CreatedByName,
                  time: item.CreatedOn,
                  text: item.Remark
                }
              }).reverse()
              this.dataListLoading = false
            }
          }))
        })
      }
    },
    handleClose () {
      // this.$refs['dataForm'].resetFields()
      this.Id = ''
      this.dataForm = {
        UserName: '',
        Sex: '1',
        BirthDate: '',
        BirthDateAge: '',
        BirthDateUnit: '1', // str
        MobilePhone: '',
        AllergyHistory: '', // 
        Address: '',
        Source: '20', // 
        Points: '',

        EditPoints: '',
        Remark: ''
      }
      this.$refs['dataForm'].clearValidate()
    },
    // 
    dataFormSubmit () {
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          var params = {
            Id: this.memberId,
            // Points: this.dataForm.Points, // 
            EditPoints: this.dataForm.EditPoints,
            Remark: this.dataForm.Remark
          }
          console.log(params)
          API.member.editIntegral(params).then((data) => {
            if (data.code === '0000') {
              this.$message({
                message: `${this.memberId ? '' : ''}`,
                type: 'success',
                duration: 1500,
                onClose: () => {
                  this.visible = false
                  this.$emit('refreshDataList')
                }
              })
            } else {
              this.$message.error(data.message)
            }
          })
        }
      })
    }

  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.time-line {
  min-height: 500px;
  height:500px;
  overflow: scroll;
  &::-webkit-scrollbar { width: 7px; }
  &::-webkit-scrollbar-thumb {
    border-radius: 3px;
    box-shadow: inset 0 0 5px rgba(0,0,0,0.1);
    background-color: #DDDEE0;
  }
  &::-webkit-scrollbar-track {
    border-radius: 0;
    box-shadow: inset 0 0 5px rgba(0,0,0,0);
    background-color: rgba(0,0,0,0);
  }
  div.time-line-items {
    position:relative;
    padding: 0 10px 20px 35px;
    i {
      position: absolute;
      left: 0;
      top: 7px;
      width: 12px;
      height: 12px;
      background-color: #E4E7ED;
      border-radius: 50%;
    }
    strong {
      position: absolute;
      left: 5px;
      top: 19px;
      width: 2px;
      height: 119px;
      background-color: #E4E7ED;
    }
    span {color: #909399;}
    div {
      height: 90px;
      padding: 7px 10px;
      overflow: hidden;
      border-radius: 5px;
      border: 1px solid #EBEEF5;
      box-shadow: 0 0 10px 0 #EBEEF5;
      p {font-size:14px;color: #333}
      font-size: 12px;
      color: #999;
    }
  }
}
</style>



