</template>
<script type="text/ecmascript-6">
import API from '@/api'
import '../common/icon/iconfont.css'
import {calcAge} from '@/utils/validate'
export default {
  data () {
    return {
      visible: false,
      dataListLoading: false,

      registerAllData: '', // 
      dataForm: {
        PaymentWay: 1 // 
      },
      pages: 1 //  
    }
  },
  watch: {
    'registerAllData.SaleOrderItems': function (val, oldval) {
      this.pages = parseInt(val.length / 10 + 1) // 
      if (val.length !== 0 && (val.length % 10) === 0) {
        this.pages-- // 10
      }
    }
  },
  methods: {
    // 
    sumCount (n) {
      return this.registerAllData.SaleOrderItems.slice((n - 1) * 10, n * 10).map(item => item.SalePrice * item.Quantity).reduce((pren, nextm) => pren + nextm).toFixed(2)
    },
    // 
    sumCountAll (arr) {
      return arr.map(item => item.SalePrice * item.Quantity).reduce((pren, nextm) => pren + nextm).toFixed(2)
    },
    // Id
    init (formId) {
      if (formId) {
        this.visible = true
        this.dataListLoading = true
        API.register.getRegisterInfo({id: formId}).then(result => {
          if (result.code === '0000') {
            result.data.BirthDate = calcAge(result.data.BirthDate)
            this.registerAllData = result.data
            this.dataListLoading = false
            console.log('', result.data)
          }
        })
      }
    },
    handleClose () {
      this.dataForm = {
        PaymentWay: 1 // 
      }
    },
    // 
    chenxiPrint (printType) {
      var idName = ''
      switch (printType) {
        case 'printRecipel':
          idName = 'printRecipel'
          break
        case 'printCashier':
          idName = 'printCashier'
          break
        case 'printOutStock':
          idName = 'printOutStock'
          break
      }
      var printHTML = document.getElementById(idName).innerHTML // 
      var page = window.open('', '_blank') // 
      page.document.write(printHTML) // 
      page.print() // 
      var userAgent = navigator.userAgent
      if ((userAgent.indexOf('compatible') > -1 && userAgent.indexOf('MSIE') > -1) || (userAgent.indexOf('Edge') > -1) || (userAgent.indexOf('Trident') > -1 && userAgent.indexOf('rv:11.0') > -1)) {
        // IE
        page.document.execCommand('print')
      } else {
        console.log('not IE')
      }
      page.close() // 
    },
    // 

    // 
    sumChinese (n) {
      if (!/^(0|[1-9]\d*)(\.\d+)?$/.test(n)) return ''
      var unit = ''
      var str = ''
      n += '00'
      var p = n.indexOf('.')
      if (p >= 0) n = n.substring(0, p) + n.substr(p + 1, 2)
      unit = unit.substr(unit.length - n.length)
      for (var i = 0; i < n.length; i++) str += ''.charAt(n.charAt(i)) + unit.charAt(i)
      return str.replace(/(|||)/g, '').replace(/()+/g, '').replace(/(||)/g, '$1').replace(/()|()/g, '$1$2').replace(/^?|/g, '').replace(/$/g, '')
    }

  }
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
  .registerIndex /deep/ .el-form-item {
