import qs from 'qs'
import merge from 'lodash/merge'

/**
 * 
 * @param {*} params 
 * @param {*} requestType 
 * @param {*} openDefultParams ?
 * @param {*} contentType 
 *  json: 'application/json; charset=utf-8'
 *  form: 'application/x-www-form-urlencoded; charset=utf-8'
 */
export default function (params, requestType = 'post', openDefultParams = false, contentType = 'json') {
  // 
  var defaults = {
    't': new Date().getTime()
  }
  params = openDefultParams ? merge(defaults, params) : params
  params = requestType === 'post' ? (contentType === 'json' ? JSON.stringify(params) : qs.stringify(params)) : params
  return params
}



