<template>
  <el-dialog
    v-dialogDrag
    :title="!dataForm.id ? '' : ''" width="600px"
    :close-on-click-modal="false"
    :visible.sync="visible" @close="handleClose">
    <el-form :model="dataForm" :rules="dataRule" ref="dataForm" label-width="80px" size="small" :inline="true">
      <el-form-item label="" prop="UserName">
        <el-input v-model="dataForm.UserName" placeholder="" style="width: 160px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="Sex">
        <el-radio-group v-model="dataForm.Sex" style="width: 144px">
          <el-radio label="1"></el-radio>
          <el-radio label="2"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="" prop="BirthDateAge">
        <el-input v-model="dataForm.BirthDateAge" @blur="stampCalc(dataForm.BirthDateAge, dataForm.BirthDateUnit)" placeholder="" style="width: 91px" clearable></el-input>
        <el-select v-model="dataForm.BirthDateUnit" @change="stampCalc(dataForm.BirthDateAge, dataForm.BirthDateUnit)" style="width: 66px;margin-right: 10px">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="0"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="BirthDate">
        <el-date-picker type="date" value-format="yyyy-MM-dd" v-model="dataForm.BirthDate"
                        placeholder="" style="width: 149px">
        </el-date-picker>
      </el-form-item>

      <el-form-item label="" prop="MobilePhone">
        <el-input v-model="dataForm.MobilePhone" placeholder="" style="width: 160px"></el-input>
      </el-form-item>
      <br>
      <el-form-item label="" prop="Source">
        <el-select v-model="dataForm.Source" style="width: 160px">
          <el-option v-for="item in memberOrigin" :key="item.lab"
                     :label="item.lab" :value="item.val">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="" prop="AllergyHistory">
        <el-input v-model="dataForm.AllergyHistory" placeholder="" style="width: 413px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="Address">
        <el-input v-model="dataForm.Address" placeholder="" style="width: 413px"></el-input>
      </el-form-item>
      <!--<el-form-item label="" prop="SalePrice">-->
      <!--<el-input v-model="dataForm.SalePrice" placeholder=""></el-input>-->
      <!--</el-form-item>-->
    </el-form>

    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false"></el-button>
      <el-button type="primary" @click="dataFormSubmit()"></el-button>
    </span>
  </el-dialog>
</template>
<script type="text/ecmascript-6">
import API from '@/api'
import {Currency, Phone, NumberInt} from '../../utils/validate'
import {formatDate, calcAge, calcTimeStamp} from '@/utils/validate'

export default {
  components: {
  },
  data () {
    return {
      visible: false,
      drugsCategoryList: [], // 
      memberId: '', // IDaddedit
      dataForm: {
        UserName: '',
        Sex: '1',
        BirthDate: '',
        BirthDateAge: '',
        BirthDateUnit: '1', // str
        MobilePhone: '',
        Source: '20', // 
        AllergyHistory: '', // 
        Address: ''
      },
      memberOrigin: [
        {lab: '', val: '1'},
        {lab: '', val: '2'},
        {lab: '', val: '3'},
        {lab: '', val: '4'},
        {lab: '', val: '5'},
        {lab: '', val: '6'},
        {lab: '', val: '20'}
      ],
      dataRule: {
        UserName: Currency(''),
        BirthDateAge: NumberInt(),
        BirthDate: Currency(''),
        Source: Currency(''),
        MobilePhone: Phone(1)
      }
    }
  },
  watch: {
    'dataForm.BirthDate': function (val, oldval) {
      if (val === null) {
        this.dataForm.BirthDateUnit = '1'
        this.dataForm.BirthDateAge = ''
      } else {
        var allAge = calcAge(val) // !!!!!!18  10 1
        if (allAge.substr(allAge.length - 1) === '') {
          this.dataForm.BirthDateUnit = '0'
        } else if (allAge.substr(allAge.length - 1) === '') {
          this.dataForm.BirthDateUnit = '1'
        }
        this.dataForm.BirthDateAge = allAge.substring(0, allAge.length - 1) // !!!!!!parseInt
      }
    }
    // 'dataForm.BirthDateAge': function (val, oldval) { // ; 
    //   if (val === '') {
    //     this.dataForm.BirthDate = ''
    //     this.dataForm.BirthDateUnit = '1'
    //     // this.dataForm.BirthDateAge = ''
    //   } else {
    //     var timeStamp = calcTimeStamp(val, this.dataForm.BirthDateUnit) // !!!!!!2019-10-02
    //     this.dataForm.BirthDate = timeStamp
    //   }
    // }
  },
  methods: {
    // age
    stampCalc (age, unit) {
      if (unit === '1' && age > 120) { // 120
        this.dataForm.BirthDateUnit = '1'
        this.dataForm.BirthDateAge = ''
        return false
      } else if (unit === '0' && age > 12) { // 12
        this.dataForm.BirthDateUnit = '1'
        this.dataForm.BirthDateAge = ''
        return false
      }

      if (age === '' || age === '0' || !/^\d+$/.test(age)) {
        this.dataForm.BirthDateUnit = '1'
        this.dataForm.BirthDateAge = ''
        return false
      }
      this.dataForm.BirthDate = calcTimeStamp(age, unit)
    },
    // info
    init (id) {
      this.visible = true
      this.memberId = id
      if (id !== undefined) {
        this.$nextTick(() => {
          API.member.editMemberGet({id: id}).then(result => {
            if (result.code === '0000') {
              this.dataForm.UserName = result.data.UserName
              this.dataForm.Sex = String(result.data.Sex)

              this.dataForm.BirthDate = formatDate(new Date(result.data.BirthDate
                .substring(6, result.data.BirthDate.length - 2) * 1), 'yyyy-MM-dd')
              var allAge = calcAge(this.dataForm.BirthDate) // !!!!!!18  10 1
              if (allAge.substr(allAge.length - 1) === '') {
                this.dataForm.BirthDateUnit = '0'
              } else if (allAge.substr(allAge.length - 1) === '') {
                this.dataForm.BirthDateUnit = '1'
              }
              this.dataForm.BirthDateAge = allAge.substring(0, allAge.length - 1) // !!!!!!parseInt

              this.dataForm.MobilePhone = result.data.MobilePhone
              this.dataForm.AllergyHistory = result.data.AllergyHistory
              this.dataForm.Address = result.data.Address
              this.dataForm.Source = String(result.data.Source)
              console.log(result.data)
              console.log(this.dataForm)
              this.memberId = result.data.Id
            }
          })
        })
      }
    },
    handleClose () {
      // this.$refs['dataForm'].resetFields()
      this.Id = ''
      this.dataForm = {
        UserName: '',
        Sex: '1',
        BirthDate: '',
        BirthDateAge: '',
        BirthDateUnit: '1', // str
        MobilePhone: '',
        AllergyHistory: '', // 
        Address: '',
        Source: '20' // 
      }
      this.$refs['dataForm'].clearValidate()
    },
    // 
    dataFormSubmit () {
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          var paramsAdd = {
            StoreId: this.$store.getters.getAccountCurrentHandleStore,
            UserName: this.dataForm.UserName,
            Sex: this.dataForm.Sex,
            BirthDate: this.dataForm.BirthDate,
            MobilePhone: this.dataForm.MobilePhone,
            AllergyHistory: this.dataForm.AllergyHistory,
            Address: this.dataForm.Address,
            Source: this.dataForm.Source
          }
          var paramsEdit = {
            StoreId: this.$store.getters.getAccountCurrentHandleStore,
            UserName: this.dataForm.UserName,
            Sex: this.dataForm.Sex,
            BirthDate: this.dataForm.BirthDate,
            MobilePhone: this.dataForm.MobilePhone,
            AllergyHistory: this.dataForm.AllergyHistory,
            Address: this.dataForm.Address,
            Source: this.dataForm.Source,
            Id: this.memberId
          }
          console.log(paramsAdd)
          console.log(paramsEdit)
          var tick = this.memberId ? API.member.editMemberSubmit(paramsEdit) : API.member.addMemberSubmit(paramsAdd)
          tick.then((data) => {
            if (data.code === '0000') {
              this.$message({
                message: `${this.memberId ? '' : ''}`,
                type: 'success',
                duration: 1500,
                onClose: () => {
                  this.visible = false
                  this.$emit('refreshDataList')
                }
              })
            } else {
              this.$message.error(data.message)
            }
          })
        }
      })
    }

  }
}
</script>



