<template>
  <el-dialog
    title=""
    :visible.sync="visible"
    :append-to-body="true">
    <el-form :model="dataForm" :rules="dataRule" ref="dataForm" @keyup.enter.native="dataFormSubmit()" label-width="94px">
      <el-form-item label="">
        <span style="color: red">{{ $store.state.user.name }}</span>
      </el-form-item>
      <el-form-item label="" prop="oldPassword">
        <el-input type="password" v-model="dataForm.oldPassword"></el-input>
      </el-form-item>
      <el-form-item label="" prop="newPassword">
        <el-input type="password" v-model="dataForm.newPassword"></el-input>
      </el-form-item>
      <el-form-item label="" prop="confirmPassword">
        <el-input type="password" v-model="dataForm.confirmPassword"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false"></el-button>
      <el-button type="primary" @click="dataFormSubmit()"></el-button>
    </span>
  </el-dialog>
</template>

<script type="text/ecmascript-6">
import API from '@/api'
import md5 from 'js-md5'
import { mapMutations } from 'vuex'
import {removeStore, removeAccountData} from '@/utils'

export default {
  data () {
    var validateConfirmPassword = (rule, value, callback) => {
      if (this.dataForm.newPassword !== value) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      visible: false,
      dataForm: {
        currentUser: '',
        name: '',

        oldPassword: '',
        newPassword: '',
        confirmPassword: ''
      },
      dataRule: {
        oldPassword: [
          { required: true, message: '', trigger: 'blur' }
        ],
        newPassword: [
          { required: true, message: '', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validateConfirmPassword, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    ...mapMutations(['DELETE_CONTENT_TABS']),
    // 
    init () {
      this.visible = true
      this.$nextTick(() => {
        this.$refs['dataForm'].resetFields()
      })
    },
    // 
    dataFormSubmit () {
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          var params = {
            // 'adminName': this.$store.state.user.name, // ???
            // 'currentUser': this.dataForm.currentUser,
            'oldPassword': '',
            'newPassword': ''
          }
          if (this.dataForm.oldPassword) {
            params.oldPassword = this.dataForm.oldPassword
            // params.oldPassword = md5(this.dataForm.oldPassword)
          }
          if (this.dataForm.newPassword) {
            params.newPassword = this.dataForm.newPassword
            // params.newPassword = md5(this.dataForm.newPassword)
          }
          console.log(params)
          API.role.changePassword(params).then((data) => {
            if (data.code === '0000') {
              this.$message({
                message: '',
                type: 'success',
                duration: 1500,
                onClose: () => {
                  this.visible = false
                  this.$nextTick(() => {
                    // this.DELETE_CONTENT_TABS([])
                    // this.$cookie.delete('token')

                    // cookie
                    this.DELETE_CONTENT_TABS([])
                    removeStore('userInfo')
                    // 2019.5.8 cx++
                    // ()(login/first-tab-add-or-update.vue)
                    // sessionsessionVuexVuex
                    // removeAccountData('accountCurrentHandleStore') 
                    this.$store.commit('setAccountCurrentHandleStore', null)
                    this.$router.replace({ name: 'login' })
                  })
                }
              })
            } else {
              this.$message.error(data.message)
            }
          })
        }
      })
    }

  }
}
</script>



